// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 1.1 引入Vue、vue-router框架
import Vue from 'vue'

// 1.2 引入ElementUI界面库
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
Vue.use(ElementUI);

// 1.3 引入滚动轴控件
import HappyScroll from "vue-happy-scroll";
import "vue-happy-scroll/docs/happy-scroll.css";
Vue.use(HappyScroll);

// 1.4 引入可以改变表格滚动轴样式的控件 用jquery的方式调用
import lzscroll from '../node_modules/lzscroll/js/lz-scroll-1.4.6.js'
import '../node_modules/lzscroll/css/lz-scroll.css'

// 2.1 统一样式文件
import "normalize.css";

// 2.2 引入字体样式
import './assets/icons/material-design-icons/material-icons.css'
import './assets/icons/ali-chart-icons/ali-chart-icons.css'

// 2.3 引入UiKit样式库
import './assets/styles/uikit/css/uikit.almost-flat.min.css'

// 2.4 引入数组处理库Lodash
import _ from "lodash";
window._ = _;

// 2.5 引入离线数据存储库
import lscache from 'lscache';
window.lscache = lscache;

// 2.6 引入时间格式处理库
const moment = require('moment');
moment.locale('zh-cn');
window.moment = moment;

// 2.7 引入jQuery
import $ from "jquery";
window.jQuery = window.$ = $;

// 2.8 引入echarts图表组件
import eCharts from 'echarts/lib/echarts.js';
Vue.prototype.$echarts = eCharts;
// 引入echarts组件 
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/pie';
import 'echarts/lib/chart/gauge';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/dataZoom';

// 3.1 引入根组件
import App from './App'

// 3.2 引入非父子组件之间的交互组件
import Bus from './components/common/EventBus.js'
Vue.prototype.$bus = Bus;

// 3.3 引入全局配置
import constants from '../static/constants.js';
window.constants = constants;

// 3.4 引入封装的Ajax操作文件
import ajaxOper from './assets/scripts/ajax/ajaxOper.js'
Vue.prototype.$ajax = ajaxOper

// 3.5 引入封装的弹框提示操作文件
import popupOper from './assets/scripts/common/popupOper.js'
Vue.prototype.$popup = popupOper;

// 3.6 引入封装的工具函数操作文件
import utilsOper from './assets/scripts/common/utilsOper.js'
window.utilsOper = utilsOper;

// 3.7 引入自定义样式文件
import './assets/styles/main.css'
import './assets/styles/subpage.css'
import './assets/styles/popup.css'
import './assets/styles/element-custom.css'

Vue.config.productionTip = false

// 引入vue-router路由框架
import router from './router'

/* eslint-disable no-new */
window.tootVm = new Vue({
    router,
    render: h => h(App)
}).$mount("#app")



