<template>
    <div class="video-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="video-left uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span>添加视频点位</span>
            </div>
            <div class="video-left-content">
                <happy-scroll class="video-form" color="rgba(73, 163, 225,0.2)" hide-horizontal resize size="4">
                    <el-form :model="videoForm" :rules="videoRules" ref="videoForm" label-width="100px">
                        <el-form-item label="位置名称" prop="videoName">
                            <el-input v-model="videoForm.videoName" clearable placeholder="请输入位置名称"></el-input>
                        </el-form-item>
                        <el-form-item label="位置编号" prop="videoNumber">
                            <el-input v-model="videoForm.videoNumber" clearable placeholder="请输入位置编号"></el-input>
                        </el-form-item>
                        <el-form-item label="视频类型" prop="type">
                            <el-select v-model="videoForm.type" clearable placeholder="请选择视频类型">
                                <el-option label="摄像头" value="摄像头"></el-option>
                                <el-option label="执法仪" value="执法仪"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="基础描述信息" prop="baseDescribeInfo">
                            <el-input v-model="videoForm.baseDescribeInfo" type="textarea" :rows="6" clearable placeholder="请输入基础信息描述"></el-input>
                        </el-form-item>
                        <el-form-item label="位置描述信息" prop="locationDescribeInfo">
                            <el-input v-model="videoForm.locationDescribeInfo" type="textarea" :rows="6" clearable placeholder="请输入位置信息描述"></el-input>
                        </el-form-item>
                    </el-form>
                </happy-scroll>
                <div slot="footer" class="bottom-footer">
                    <el-button size="small" class="btn-common btn-back" @click="handleReset()">重置</el-button>
                    <el-button size="small" class="btn-common btn-ok" @click="handleSave()">保存</el-button>
                </div>
            </div>
        </div>
        <div class="uk-flex-item-none" style="width:1000px;">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>视频列表</span>
            </div>
            <div class="video-right-content">
                <div class="demo-input-suffix">
                    <span>视频类型：</span>
                    <el-select v-model="type" placeholder="请选择视频类型" @change="videoListQuery">
                        <el-option label="摄像头" value="摄像头"></el-option>
                        <el-option label="执法仪" value="执法仪"></el-option>
                    </el-select>
                </div>
                <el-table class="video-table" :data="videoList" highlight-current-row>
                    <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                    <el-table-column prop="videoName" label="视频名称" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="videoNumber" label="视频编号" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="type" label="视频类型" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="baseDescribeInfo" label="基础描述信息" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="locationDescribeInfo" label="位置描述信息" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="操作" align="center" width="120">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
      /** 视频列表 */
      videoList: [{
          videoName: '摄像头1',
          videoNumber: '001',
          type: '摄像头',
          baseDescribeInfo: '这是一个监控探头',
          locationDescribeInfo: '位于xxx街与xxx路交叉口向东50米'
      }],
      /** 添加视频表单 */
      videoForm: {},
      videoRules: {},

      type: ""
    };
  },

  created() {},

  mounted() {},

  methods: {
    /** 重置表单 */
    handleReset() {},
    /** 保存视频信息 */
    handleSave() {},

    /** 编辑表单修改视频信息 */
    handleEdit(index, row) {},

    /** 查询视频列表 */
    videoListQuery() {},

    /** 删除视频 */
    handleDelete(index, row) {}
  }
};
</script>

<style lang="sass">
@import './video_manage.scss'
</style>




