<template>
  <div id="map">
  </div>
</template>

<script>
import mapApi from "./main";

export default {
  data() {
    return {
      map: null,
      mapApi,
    };
  },
  mounted() {
    this.map = mapApi.init();
  }
};
</script>

<style scoped>
#map {
  width: 100%;
  height: 100%;
}
</style>



