<template>
    <div class="video-monitor-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="video-monitor-left uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>视频列表</span>
            </div>
            <div class="left-content">
                <happy-scroll class="video-monitor-tree" color="rgba(73, 163, 225,0.2)" hide-horizontal resize size="4">
                    <el-tree :data="videoMonitorData" accordion @node-click="handleNodeClick"></el-tree>
                </happy-scroll>
            </div>
        </div>
        <div class="video-monitor-right uk-flex-item-none">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE05C;</i>
                </span>
                <span>视频画面</span>
            </div>
            <div class="right-content">
                <div class="video-tableau">
                    <el-card class="video-card" :body-style="{ padding: '0px' }">
                        <my-video :sources="video1.sources" :options="video1.options"></my-video>
                    </el-card>
                    <el-card class="video-card" :body-style="{ padding: '0px' }">
                        <my-video :sources="video2.sources" :options="video2.options"></my-video>
                    </el-card>
                    <el-card class="video-card" :body-style="{ padding: '0px' }">
                        <my-video :sources="video3.sources" :options="video3.options"></my-video>
                    </el-card>
                    <el-card class="video-card" :body-style="{ padding: '0px' }">
                        <my-video :sources="video4.sources" :options="video4.options"></my-video>
                    </el-card>
                </div>
                <div class="video-console">
                    <el-button-group>
                        <el-button type="primary" icon="uk-icon-stop-circle-o"></el-button>
                        <el-button type="primary" icon="uk-icon-th-large"></el-button>
                        <el-button type="primary" icon="uk-icon-th"></el-button>
                    </el-button-group>
                    <el-button-group>
                        <el-button type="primary" icon="uk-icon-fast-backward"></el-button>
                        <el-button type="primary" icon="uk-icon-pause"></el-button>
                        <el-button type="primary" icon="uk-icon-play"></el-button>
                        <el-button type="primary" icon="uk-icon-fast-forward"></el-button>
                    </el-button-group>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./real_time_service.js";

import myVideo from "vue-video";

export default {
  components: {
    myVideo
  },
  data() {
    return {
      videoMonitorData: [
        {
          label: "操作间",
          children: [
            {
              label: "摄像头 1",
              value: "摄像头 1"
            },
            {
              label: "摄像头 2",
              value: "摄像头 2"
            },
            {
              label: "摄像头 3",
              value: "摄像头 3"
            },
            {
              label: "摄像头 4",
              value: "摄像头 4"
            },
            {
              label: "摄像头 5",
              value: "摄像头 5"
            }
          ]
        },
        {
          label: "检疫间",
          children: [
            {
              label: "摄像头 1",
              value: "摄像头 1"
            },
            {
              label: "摄像头 2",
              value: "摄像头 2"
            },
            {
              label: "摄像头 3",
              value: "摄像头 3"
            },
            {
              label: "摄像头 4",
              value: "摄像头 4"
            },
            {
              label: "摄像头 5",
              value: "摄像头 5"
            }
          ]
        },
        {
          label: "待宰间",
          children: [
            {
              label: "摄像头 1",
              value: "摄像头 1"
            },
            {
              label: "摄像头 2",
              value: "摄像头 2"
            }
          ]
        }
      ],
      video1: {
        sources: [
          {
            src: "https://pic.ibaotu.com/00/21/12/82G888piCeNq.mp4",
            type: "video/mp4"
          }
        ],
        options: {
          autoplay: false,
          volume: 0.6,
          poster: "http:////pic.ibaotu.com/00/21/82/92r888piCqak.jpg"
        }
      },
      video2: {
        sources: [
          {
            src: "http://video.699pic.com/videos/96/59/31/fCh78RPV3vA31521965931.mp4",
            type: "video/mp4"
          }
        ],
        options: {
          autoplay: false,
          volume: 0.6,
          poster: "http://img95.699pic.com/video_cover/96/58/43/FziBErfpYdDb1521965843.JPG"
        }
      },
      video3: {
        sources: [
          {
            src: "http://img95.699pic.com/videos/2016/09/05/63764b2c-1e97-4356-8648-6d8a31b1abe2.mp4",
            type: "video/mp4"
          }
        ],
        options: {
          autoplay: false,
          volume: 0.6,
          poster: "http://img95.699pic.com/videos_thumbnail/2016/09/05/63764b2c-1e97-4356-8648-6d8a31b1abe2.jpg"
        }
      },
      video4: {
        sources: [
          {
            src: "http://img95.699pic.com/videos/2016/09/19/64d91ae0-74b7-4096-9050-076076399c15.mp4",
            type: "video/mp4"
          }
        ],
        options: {
          autoplay: false,
          volume: 0.6,
          poster: "http://img95.699pic.com/videos_thumbnail/2016/09/19/64d91ae0-74b7-4096-9050-076076399c15.jpg"
        }
      },
      
    };
  },

  created() {},

  mounted() {},

  methods: {
    handleNodeClick() {}
  }
};
</script>

<style lang="sass">
@import './video_monitor.scss'
</style>




