<template>
  <div class="manage-home-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="manage-home-content uk-flex uk-flex-column uk-flex-nowrap">
            <el-row class="top-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="one-card-item" v-for="(itemMenu, indexMenu) in outInData" :key="indexMenu">
                        <div class="in-out-title">
                            <span class="title">{{indexMenu == 0 ? '今日流入' : '今日流出' }}</span>
                            <div class="in-out-slick">
                                <div class="slick-item" v-for="(item, index) in itemMenu" :key="index">
                                    <img :src="item.imgSrc">
                                    <span :title="'共计 ' + item.total + ' 公斤'">{{ item.type }} 共计 {{ item.total }} 公斤</span>
                                    <span>周同比</span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                </div>
                            </div>
                        </div>
                        <div class="one-card-content">
                            <div class="one-content-item" v-for="(item, index) in itemMenu" :key="index">
                                <div class="title">{{ item.type }}流入</div>
                                <div class="mid">
                                    <img :src="item.imgSrc">
                                    <span :title="'共计 ' + item.total + ' 公斤'">{{ item.type }} 共计 {{ item.total }} 公斤</span>
                                </div>
                                <div class="bottom">
                                    <span>周同比</span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                    <span>{{ item.type }} {{item.number}} 头</span>
                                </div>
                            </div>
                        </div>
                      </div>
                  </div>
              </el-col>
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title">流入产出趋势</div>
                      <div class="two-btn">
                        <el-button size="mini" type="primary">今日</el-button>
                        <el-button size="mini" type="primary">本周</el-button>
                        <el-button size="mini" type="primary">本月</el-button>
                        <el-button size="mini" type="primary">全年</el-button>
                        <el-date-picker v-model="inOutDate" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" @change="handleCheckDate"></el-date-picker>
                      </div>
                      <div class="in-out-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="8">
                  <div class="card">
                      <div class="title">肉类占比图</div>
                      <div class="three-btn">
                        <el-button size="mini" type="primary">全部肉类</el-button>
                        <el-button size="mini" type="primary">大类</el-button>
                        <el-button size="mini" type="primary">小类</el-button>
                      </div>
                      <div class="pie-echarts"></div>
                  </div>
              </el-col>
            </el-row>
            <el-row class="bittom-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title">人员出勤情况统计图</div>
                      <div class="bar-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="16">
                  <div class="card">
                      <div class="title">人员缺岗情况</div>
                      <div class="total">
                          <span>请假 <span style="color: #F64D46">2</span> 人</span>
                          <el-select v-model="userStatus" clearable placeholder="请选择员工缺岗状态">
                            <el-option label="请假" value="请假"></el-option>
                            <el-option label="旷工" value="旷工"></el-option>
                          </el-select>
                      </div>
                      <el-table class="user-table" :data="userData" highlight-current-row height="calc(100% - 109px)">
                        <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                        <el-table-column prop="name" label="姓名" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="location" label="车间" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="status" label="状态" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <el-tag :type="scope.row.status == 1 ? 'success' : 'danger'"></el-tag>
                                {{scope.row.status == 1 ? '请假' : '旷工'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="type" label="类型" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="langTime" label="时常(天)" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="startTime" label="开始时间" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="endTime" label="结束时间" align="center" show-overflow-tooltip></el-table-column>
                      </el-table>
                      <div class="main-page">
                          <el-pagination @size-change="handleSize" @current-change="handleCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                          </el-pagination>
                      </div>
                  </div>
              </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
import service from "./home_service.js";
// 轮播
import Slick from "vue-slick";

export default {
  components: {
    Slick
  },
  data() {
    return {
      lineCharts: null,
      pieCharts: null,
      barCharts: null,
      outInData: [
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "猪",
            total: "25420.8",
            trend: "el-icon-caret-top",
            number: "104",
            percent: "12.5"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "牛",
            total: "1250.3",
            trend: "el-icon-caret-bottom",
            number: "56",
            percent: "5.9"
          },
          {
            imgSrc: require("../../assets/images/home/yang.png"),
            type: "羊",
            total: "8652.9",
            trend: "el-icon-caret-top",
            number: "238",
            percent: "15.9"
          }
        ],
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "猪",
            total: "21001.3",
            trend: "el-icon-caret-top",
            number: "86",
            percent: "9.4"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "牛",
            total: "19502.5",
            trend: "el-icon-caret-bottom",
            number: "78",
            percent: "3.1"
          }
        ]
      ],
      /** 流入产出折线图配置 */
      lineOptions: {
        title: {
          text: "流入产出对比图",
          x: "center"
        },
        tooltip: {
          trigger: "axis",
          formatter: "{b}<br/>{a0}: {c0} 万斤 <br/> {a1}: {c1} 万斤 "
        },
        legend: {
          data: ["流入", "产出"],
          textStyle: {
            color: "#383838"
          },
          top: 40
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: {
          type: "category",
          name: "星期",
          boundaryGap: true,
          data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
          axisLine: {
            lineStyle: {
              color: "#383838"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "流入（万斤）",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          },
          {
            type: "value",
            name: "产出（万斤）",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "流入",
            data: [20, 32, 51, 34, 29, 33, 32],
            type: "line",
            smooth: true,
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "#0EB206"
                  },
                  {
                    offset: 1,
                    color: "#FEF6F3"
                  }
                ],
                globalCoord: false
              }
            },
            itemStyle: {
              normal: {
                color: "#0EB206"
              }
            }
          },
          {
            name: "产出",
            data: [24, 35, 55, 45, 32, 44, 42],
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "#f72020"
                  },
                  {
                    offset: 1,
                    color: "#FEF6F3"
                  }
                ],
                globalCoord: false
              }
            },
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      inOutDate: "",
      /** 肉类分类占比饼图配置项 */
      pieOptions: {
        title: {
          text: "肉类分类占比统计图",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} 万斤 ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["猪", "牛", "羊"]
        },
        series: [
          {
            name: "肉类占比",
            type: "pie",
            radius: ["40%", "55%"],
            selectedMode: "single",
            label: {
              normal: {
                show: false,
                position: "left",
                formatter: "{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c} {per|{d}%}",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                shadowBlur: 3,
                rich: {
                  a: {
                    color: "#999",
                    lineHeight: 22,
                    align: "center"
                  },
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 12034, name: "猪", selected: true },
              { value: 8350, name: "牛" },
              { value: 9010, name: "羊" }
            ]
          }
        ]
      },
      /** 人员到岗情况柱状图配置 */
      barOptions: {
        tooltip: {
          trigger: "axis",
          formatter: values => {
            let title = "";
            let content = "";
            let total = 0;
            _.map(values, item => {
              title = item.name;
              content += "<br/>" + item.seriesName + " : " + item.value + " 人";
              total += item.value;
            });
            return title + " : " + total + " 人" + content;
          }
        },
        legend: {
          data: ["到岗", "请假", "旷工"]
        },
        grid: {
          bottom: 30,
          left: 30,
          right: 40,
          top: 40
        },
        xAxis: [
          {
            type: "category",
            name: "区域",
            boundaryGap: true,
            data: [
              "卸载区",
              "待宰栏",
              "刺杀车间",
              "去毛车间",
              "检疫车间",
              "开膛车间",
              "无害化处理车间"
            ],
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "人数",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "到岗",
            type: "bar",
            barWidth: 25,
            stack: "出勤统计",
            data: [12, 14, 10, 7, 15, 5, 11],
            smooth: true,
            itemStyle: {
              normal: {
                color: "#4CCB72"
              }
            }
          },
          {
            name: "请假",
            type: "bar",
            barWidth: 25,
            stack: "出勤统计",
            data: [1, 0, 0, 1, 0, 0, 0],
            smooth: true,
            itemStyle: {
              normal: {
                color: "#206FE5"
              }
            }
          },
          {
            name: "旷工",
            data: [0, 0, 0, 1, 0, 0, 0],
            type: "bar",
            barWidth: 25,
            stack: "出勤统计",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#F3637C"
              }
            }
          }
        ]
      },
      userData: [
        {
          name: "王五",
          location: "卸载区",
          type: "事假",
          langTime: "3",
          status: "1",
          startTime: "2018-5-3",
          endTime: "2018-5-6"
        },
        {
          name: "刘启",
          location: "检疫车间",
          type: "病假",
          langTime: "2",
          status: "1",
          startTime: "2018-5-1",
          endTime: "2018-5-3"
        },
        {
          name: "钱八",
          location: "检疫车间",
          type: "",
          langTime: "",
          status: "0",
          startTime: "",
          endTime: ""
        }
      ],
      userStatus: "",
      /** 人员表格每页展示的数据量 */
      pageSize: 15,
      /** 人员表格总数据量 */
      total: 3,
      /** 当前是第几页 */
      page: 1
    };
  },
  mounted() {
    /** 流入流出滚动轴样式 */
    $(".one-card-content").lzscroll("y", 60, "hover", "mytheme");

    /** 流入产出折线图 */
    let lineDom = $(".in-out-echarts")[0];
    this.lineCharts = this.$echarts.init(lineDom);
    this.lineCharts.setOption(this.lineOptions);

    /** 肉类分类饼图 */
    let pieDom = $(".pie-echarts")[0];
    this.pieCharts = this.$echarts.init(pieDom);
    this.pieCharts.setOption(this.pieOptions);

    /** 人员到岗情况柱状图 */
    let barDom = $(".bar-echarts")[0];
    this.barCharts = this.$echarts.init(barDom);
    this.barCharts.setOption(this.barOptions);

    $(".in-out-slick").slick({
      slidesToShow: 1,
      autoplaySpeed: '1000',
      arrows: false,
      vertical: true,
      autoplay: true,
      infinite: true
    });
  },
  created() {},
  watch: {},
  methods: {
    /** 选中时间获取流入产出趋势数据 */
    handleCheckDate() {},

    /** 表格修改每页数量后执行的函数 */
    handleSize(val) {
      this.userData = [];
      this.pageSize = val;
      this.userDataQuery(1);
    },

    /** 表格页码改变后执行的函数 */
    handleCurrent(val) {
      this.userData = [];
      this.page = val;
      this.userDataQuery();
    },

    /** 查询检疫人员列表 */
    userDataQuery(pg) {}
  }
};
</script>

<style lang="sass">
@import './home_page.scss';

@import '../../../node_modules/slick-carousel/slick/slick.css';
</style>



