<template>
  <div class="sewage-monitor-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="sewage-monitor-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>排放点监测</span>
            </div>
            <div class="content">
                <div class="demo-input-suffix">
                    <span>排放点：</span>
                    <el-select v-model="sewageMonitor" clearable placeholder="请选择排放点">
                        <el-option label="排放点一" value="排放点一"></el-option>
                        <el-option label="排放点二" value="排放点二"></el-option>
                    </el-select>
                </div>
                <el-collapse class="norm-coll" v-model="sewageMonitorNorm" accordion>
                    <el-collapse-item v-for="(item, index) in normData" :key="index" :name="item.nameId">
                        <template slot="title">
                            <span>{{ item.title }}</span>
                            <div class="norm-title">
                                <el-tag :type="item.type == 1 ? 'success' : 'danger'"></el-tag>
                                <span class="status">{{ item.status }}</span>
                            </div>
                        </template>
                        <el-table class="norm-table" :data="item.data" highlight-current-row :show-header="false">
                            <el-table-column prop="value" label="指标名称" align="left" show-overflow-tooltip width="40">
                                <template slot-scope="scope">
                                    <el-tag :type="Number(scope.row.value) > Number(scope.row.norm.split('~')[1]) ? 'danger' : 'success'" close-transition></el-tag>
                                </template>
                            </el-table-column>
                            <el-table-column prop="name" label="指标名称" align="left" show-overflow-tooltip width="200"></el-table-column>
                            <el-table-column prop="value" label="指标值" align="center" show-overflow-tooltip width="100"></el-table-column>
                            <el-table-column prop="unit" label="单位" align="left" show-overflow-tooltip width="100"></el-table-column>
                            <el-table-column prop="value" label="指标值" align="left" show-overflow-tooltip width="300">
                                <template slot-scope="scope">
                                    <el-progress :stroke-width="18" :text-inside="true"
                                        :percentage="Number(scope.row.value) > Number(scope.row.norm.split('~')[1]) ? 100 : Number(((scope.row.value / scope.row.norm.split('~')[1]) * 100).toFixed(0))" 
                                        :status="Number(scope.row.value) > Number(scope.row.norm.split('~')[1]) ? 'exception' : 'success'">
                                    </el-progress>
                                </template>
                            </el-table-column>
                            <el-table-column prop="norm" label="指标标准范围" align="left" show-overflow-tooltip></el-table-column>
                        </el-table>
                    </el-collapse-item>
                </el-collapse>
                <div class="norm-echarts">
                    <el-row type="flex" :gutter="10">
                      <el-col :span="12">
                        <div class="title">污染指标柱状图</div>
                        <div class="bar-echarts"></div>
                      </el-col>
                      <el-col :span="12">
                        <div class="title">排放点污染指标变化趋势图</div>
                        <div class="line-echarts"></div>
                      </el-col>
                    </el-row>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./real_time_service.js";

export default {
  data() {
    return {
      sewageMonitor: "排放点一",
      sewageMonitorNorm: "1",
      normData: [
        {
          nameId: "1",
          title: "01 - 湿基值",
          type: "1",
          status: "全部正常",
          data: [
            {
              name: "湿烟气流量",
              value: "1022316.8",
              unit: "m^3/h",
              norm: "正常范围：0~20000000"
            },
            {
              name: "粉尘(湿)",
              value: "777.2",
              unit: "mg/m^3",
              norm: "正常范围：0~1000"
            }
          ]
        },
        {
          nameId: "2",
          title: "02 - 干基值",
          type: "0",
          status: "2 项异常",
          data: [
            {
              name: "干烟气流量",
              value: "28400.3",
              unit: "m^3/h",
              norm: "正常范围：0~20000000"
            },
            {
              name: "粉尘(实测)",
              value: "14944.3",
              unit: "mg/m^3",
              norm: "正常范围：0~1000"
            },
            {
              name: "SO2(实测)",
              value: "14361.8",
              unit: "mg/m^3",
              norm: "正常范围：0~15000"
            },
            {
              name: "NO(实测)",
              value: "5744.8",
              unit: "mg/m^3",
              norm: "正常范围：0~6000"
            },
            {
              name: "NOx(实测)",
              value: "9249.1",
              unit: "mg/m^3",
              norm: "正常范围：0~9000"
            }
          ]
        },
        {
          nameId: "3",
          title: "03 - 折算值",
          type: "0",
          status: "3 项异常",
          data: [
            {
              name: "烟气流速",
              value: "56.8",
              unit: "m/s",
              norm: "正常范围：0~100"
            },
            {
              name: "粉尘(折算)",
              value: "15850",
              unit: "mg/m^3",
              norm: "正常范围：0~1000"
            },
            {
              name: "SO2(折算)",
              value: "15232.2",
              unit: "mg/m^3",
              norm: "正常范围：0~15000"
            },
            {
              name: "NOx(折算)",
              value: "9809.7",
              unit: "mg/m^3",
              norm: "正常范围：0~9000"
            }
          ]
        },
        {
          nameId: "4",
          title: "04 - 排放量",
          type: "1",
          status: "全部正常",
          data: [
            {
              name: "粉尘(排放率)",
              value: "424.4",
              unit: "kg/h",
              norm: "正常范围：0~1000"
            },
            {
              name: "SO2(排放率)",
              value: "407.9",
              unit: "kg/h",
              norm: "正常范围：0~7500"
            },
            {
              name: "NOx(排放率)",
              value: "262.7",
              unit: "kg/h",
              norm: "正常范围：0~4500"
            }
          ]
        },
        {
          nameId: "5",
          title: "05 - 环境参数",
          type: "1",
          status: "全部正常",
          data: [
            {
              name: "O2",
              value: "10",
              unit: "%",
              norm: "正常范围：0~14"
            },
            {
              name: "实测流速",
              value: "56.8",
              unit: "m/s",
              norm: "正常范围：0~60"
            },
            {
              name: "烟气温度",
              value: "286.8",
              unit: "℃",
              norm: "正常范围：0~300"
            },
            {
              name: "烟气湿度",
              value: "94.8",
              unit: "%",
              norm: "正常范围：0~100"
            },
            {
              name: "烟气压力",
              value: "9574.8",
              unit: "Pa",
              norm: "正常范围：-10000~10000"
            }
          ]
        },
        {
          nameId: "6",
          title: "06 - 废水参数",
          type: "0",
          status: "2 项异常",
          data: [
            {
              name: "ph",
              value: "8.1",
              unit: "",
              norm: "正常范围：6.9~7.1"
            },
            {	
              name: "CODcr",
              value: "1000",
              unit: "mg.L-1",
              norm: "正常范围：900~2200"
            },
            {
              name: "BOD5",
              value: "1100",
              unit: "mg.L-1",
              norm: "正常范围：500~1200"
            },
            {
              name: "SS",
              value: "900",
              unit: "mg.L-1",
              norm: "正常范围：800~1000"
            },
            {
              name: "油类",
              value: "60",
              unit: "mg.L-1",
              norm: "正常范围：25~50"
            },
          ]
        }
      ],
      barCharts: null,
      /** 污染指标柱状图配置 */
      barOptions: {
        title: {
          text: "污染指标柱状图",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: [
          {
            type: "category",
            name: "指标",
            boundaryGap: true,
            data: [
              "SO2",
              "粉尘",
              "氨氮",
              "COD",
              "高锰酸盐指数",
              "总硬度",
              "废气",
              "噪声"
            ],
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "数值",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "数值",
            data: [24, 28, 45, 95, 24, 68, 56, 51],
            type: "bar",
            barWidth: "50%",
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      lineCharts: null,
      /** 指标变化趋势折线图配置 */
      lineOptions: {
        title: {
          text: "污染指标变化趋势",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: [
            "SO2",
            "粉尘",
            "氨氮",
            "COD",
            "高锰酸盐指数",
            "总硬度",
            "废气",
            "噪声"
          ],
          textStyle: {
            color: "#383838"
          },
          top: 40
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: {
          type: "category",
          name: "日期",
          boundaryGap: true,
          data: [
            "2018-5-1",
            "2018-5-2",
            "2018-5-3",
            "2018-5-4",
            "2018-5-5",
            "2018-5-6",
            "2018-5-7",
            "2018-5-8",
            "2018-5-9",
            "2018-5-10"
          ],
          axisLine: {
            lineStyle: {
              color: "#383838"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "指标值",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "SO2",
            data: [20, 32, 91, 34, 29, 33, 32, 53, 21],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#0EB206"
              }
            }
          },
          {
            name: "粉尘",
            data: [24, 28, 45, 95, 24, 68, 56, 41, 35, 61],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#0000FF"
              }
            }
          },
          {
            name: "氨氮",
            data: [34, 31, 12, 81, 43, 14, 40, 21, 45, 41],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#FF3399"
              }
            }
          },
          {
            name: "COD",
            data: [4, 8, 14, 19, 24, 20, 10, 5, 12, 18],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#FF99CC"
              }
            }
          },
          {
            name: "高锰酸钾指数",
            data: [17, 20, 56, 13, 22, 5, 24, 12, 9, 4],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#9933FF"
              }
            }
          },
          {
            name: "总硬度",
            data: [31, 38, 20, 76, 79, 51, 42, 31, 20, 11],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#FF9933"
              }
            }
          },
          {
            name: "废气",
            data: [51, 64, 69, 59, 70, 75, 86, 74, 64, 51],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#CCFF33"
              }
            }
          },
          {
            name: "噪声",
            data: [78, 70, 73, 79, 83, 88, 69, 89, 91, 77],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#660066"
              }
            }
          }
        ]
      }
    };
  },

  mounted() {
    /** 表格记录滚动轴样式 */
    $(".el-collapse").lzscroll("y", 60, "hover", "mytheme");
    /** 污染指标柱状图 */
    let barDom = $(".bar-echarts")[0];
    this.barCharts = this.$echarts.init(barDom);
    this.barCharts.setOption(this.barOptions);

    /** 污染指标变化趋势折线图 */
    let lineDom = $(".line-echarts")[0];
    this.lineCharts = this.$echarts.init(lineDom);
    this.lineCharts.setOption(this.lineOptions);
  },

  created() {},

  watch: {},

  methods: {}
};
</script>

<style lang="sass">
@import './sewage_monitor.scss'
</style>



