<template>
  <div class="slaughte-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="slaughte-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>屠宰场管理</span>
                <span class="slaughte-add">
                    <i class="md-icon material-icons" title="添加" @click="handleSaveSlaughte">&#xE145;</i>
                </span>
            </div>
            <div class="content">
                <el-form :model="slaughteForm" :rules="slaughteRules" ref="slaughteForm" label-width="120px">
                  <el-row>
                    <el-col :span="12">
                        <el-form-item label="屠宰场名称">
                            <el-input v-model="slaughteForm.name" clearable placeholder="请输入屠宰场名称"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设计屠宰量">
                            <el-input class="min" v-model="slaughteForm.min" clearable placeholder="请输入设计屠宰量"></el-input>
                            <span>-</span>
                            <el-input class="max" v-model="slaughteForm.max" clearable placeholder="请输入设计屠宰量"></el-input>
                        </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
                <div slot="footer" class="bottom-footer">
                    <el-button size="mini" class="btn-common btn-back" @click="handleReset()">重置</el-button>
                    <el-button size="mini" class="btn-common btn-query" @click="handleSave()">保存</el-button>
                </div>
                <el-table class="slaughte-table" :data="slaughteData" highlight-current-row height="calc(100% - 127px)">
                    <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                    <el-table-column prop="name" label="屠宰场名称" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="type" label="屠宰类别" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="number" label="职工人数" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="total" label="设计屠宰量" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="user" label="责任人" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="phone" label="责任人电话" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="操作" align="center" width="170">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleUpdateSlaughte(scope.$index, scope.row)">编辑</el-button>
                            <el-button class="btn-view" size="mini" @click="handleViewSlaughte(scope.$index, scope.row)">查看</el-button>
                            <el-button type="danger" size="mini" @click="handleDeleteSlaughte(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="main-page">
                    <el-pagination @size-change="handleSlaughteSize" @current-change="handleSlaughteCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
export default {
  data() {
    return {
      slaughteForm: {
        name: "",
        min: "",
        max: ""
      },
      slaughteRules: {},
      /** 设备表格每页展示的数据量 */
      pageSize: 15,
      /** 设备表格总数据量 */
      total: 3,
      /** 当前是第几页 */
      page: 1,
      slaughteData: [
        {
          name: "城南屠宰场",
          type: "猪",
          number: "18",
          total: "5500",
          user: "张三",
          phone: "13258654528"
        },
        {
          name: "牛牛屠宰场",
          type: "牛",
          number: "21",
          total: "8000",
          user: "王五",
          phone: "13958462542"
        },
        {
          name: "城东屠宰场一分厂",
          type: "猪",
          number: "13",
          total: "3500",
          user: "林琳",
          phone: "13456852459"
        }
      ]
    };
  },
  mounted() {},
  created() {},
  watch: {},
  methods: {
    /** 表格修改每页数量后执行的函数 */
    handleSlaughteSize(val) {
      this.slaughteData = [];
      this.pageSize = val;
      this.slaughteDataQuery();
    },

    /** 表格页码改变后执行的函数 */
    handleSlaughteCurrent(val) {
      this.slaughteData = [];
      this.page = val;
      this.slaughteDataQuery();
    },

    /** 查询屠宰场列表 */
    slaughteDataQuery() {},

    /** 添加 */
    handleSaveSlaughte(index, row) {},

    /** 修改 */
    handleUpdateSlaughte(index, row) {},

    /** 查看 */
    handleViewSlaughte(index, row) {},

    /** 删除 */
    handleDeleteSlaughte(index, row) {}
  }
};
</script>

<style lang="sass">
@import './slaughte_manage.scss'
</style>



