<template>
  <div id="container" class="uk-flex uk-flex-column uk-flex-nowrap">
    <v-head ref="header"></v-head>
    <!-- 主页面 -->
    <div class="main uk-flex uk-flex-row uk-flex-nowrap uk-flex-item-1">
      <v-sidebar ref="sidebar"></v-sidebar>
      <transition name="fade" mode="out-in">
        <router-view ref="main" class="center uk-flex-item-1"></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
import vHead from "./Header.vue";
import vSidebar from "./Sidebar.vue";
import elementResizeEvent from "element-resize-event";

export default {
  data() {
    return {};
  },
  components: {
    vHead,
    vSidebar
  },
  mounted() {
    let that = this;

    /** 设置浏览器窗口大小改变事件 */
    elementResizeEvent(this.$el, function() {
      that.$bus.$emit("triScreenResize", {
        width: document.body.clientWidth,
        height: document.body.clientHeight
      });
    });
  }
};
</script>

<style>
#container,
.main {
  width: 100%;
  height: 100%;
}

.center {
  overflow-y: auto;
  padding: 0 5px 5px 5px;

  width: calc(100% - 10px);
  height: 100%;

  box-sizing: border-box;
  background-color: #ffffff;

  font-family: 微软雅黑;
}
</style>


