<template>
  <div class="quarantine-add-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="quarantine-add-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span>添加检疫</span>
            </div>
            <div class="content">
                <el-form :model="quarantineForm" :rules="quarantineRules" ref="quarantineForm" label-width="100px">
                    <el-row type="flex" :gutter="5">
                        <el-col :span="8">
                            <el-form-item label="入场单号">
                                <el-input v-model="quarantineForm.inCode" clearable placeholder="请输入入场单号"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="样品编号">
                                <el-input v-model="quarantineForm.number" clearable placeholder="请输入样品编号"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="检疫员">
                                <el-select v-model="quarantineForm.user" clearable placeholder="请选择检疫员">
                                    <el-option label="张三" value="张三"></el-option>
                                    <el-option label="李四" value="李四"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <div class="quarantine-add-scroll">
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span>头蹄及体表</span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-table class="quarantine-add-table" :data="quarantineData01" highlight-current-row :show-header="false">
                                    <el-table-column prop="name" label="指标名称" header-align="left" show-overflow-tooltip width="200">
                                        <template slot-scope="scope">
                                            <el-checkbox>{{scope.row.name}}</el-checkbox>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="values" label="指标值" header-align="left" show-overflow-tooltip>
                                        <template slot-scope="scope">
                                            <el-checkbox-group v-model="quarantineForm.shijianValue">
                                                <el-checkbox v-for = "(item, index) in scope.row.values" :key="index" :label="item"></el-checkbox>
                                            </el-checkbox-group>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span>内脏</span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-table class="quarantine-add-table" :data="quarantineData02" highlight-current-row :show-header="false">
                                    <el-table-column prop="name" label="指标名称" header-align="left" show-overflow-tooltip width="200">
                                        <template slot-scope="scope">
                                            <el-checkbox>{{scope.row.name}}</el-checkbox>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="values" label="指标值" header-align="left" show-overflow-tooltip>
                                        <template slot-scope="scope">
                                            <el-checkbox-group v-model="quarantineForm.shijianValue">
                                                <el-checkbox v-for = "(item, index) in scope.row.values" :key="index" :label="item"></el-checkbox>
                                            </el-checkbox-group>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span>胴体</span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-table class="quarantine-add-table" :data="quarantineData03" highlight-current-row :show-header="false">
                                    <el-table-column prop="name" label="指标名称" header-align="left" show-overflow-tooltip width="200">
                                        <template slot-scope="scope">
                                            <el-checkbox>{{scope.row.name}}</el-checkbox>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="values" label="指标值" header-align="left" show-overflow-tooltip>
                                        <template slot-scope="scope">
                                            <el-checkbox-group v-model="quarantineForm.shijianValue">
                                                <el-checkbox v-for = "(item, index) in scope.row.values" :key="index" :label="item"></el-checkbox>
                                            </el-checkbox-group>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span>其他</span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-form-item label="备注" class="quarantine-add-remark">
                                    <el-input v-model="quarantineForm.remark" type="textarea" :rows="6" placeholder="请输入备注"></el-input>
                                </el-form-item>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-form-item label="检疫结果">
                            <el-radio v-model="quarantineForm.jieguo" label="1">通过</el-radio>
                            <el-radio v-model="quarantineForm.jieguo" label="0">不通过</el-radio>
                        </el-form-item>
                        <el-form-item v-show="quarantineForm.jieguo == 0" label="未通过原因">
                            <el-input v-model="quarantineForm.yuanyin" type="textarea" :rows="6" placeholder="请输入检疫未通过原因"></el-input>
                        </el-form-item>
                        <el-form-item label="检疫结论">
                            <el-input v-model="quarantineForm.remark" type="textarea" :rows="6" placeholder="请输入检疫结论"></el-input>
                        </el-form-item>
                        <el-form-item label="检疫附件">
                            <el-upload action=""
                                :on-preview="handlePreview"
                                :on-remove="handleRemove"
                                :before-remove="beforeRemove"
                                multiple
                                :limit="3"
                                :on-exceed="handleExceed"
                                :file-list="fileList">
                                <el-button size="small" type="primary">浏览</el-button>
                                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>
                        <div slot="footer" class="bottom-footer">
                            <el-button size="small" class="btn-common btn-back" @click="handleReset()">重置</el-button>
                            <el-button size="small" class="btn-common btn-ok" @click="handleSave()">保存</el-button>
                        </div>
                    </div>
                </el-form>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./quarantine_service.js";
export default {
  data () {
      return {
          quarantineForm: {
              shijianValue: [],
              jieguo: '1',
          },
          fileList: [],
          quarantineRules: {},
          quarantineData01: [{
              name: '视检',
              values: ['体表完整','颜色正常','无关节肿大','无皮肤病变']
          }, {
              name: '观察',
              values: ['吻突无水疱、溃疡、烂斑等','齿龈无水疱、溃疡、烂斑等','蹄部无水疱、溃疡、烂斑等']
          }, {
              name: '放血视检',
              values: ['无肿大、坏死灶（紫、黑、灰、黄）', '切面呈砖红色', '周围无水肿、胶样浸润']
          },{
              name: '剖检',
              values: ['无猪囊尾蚴']
          }],
          quarantineData02: [{
              name: '取前观察',
              values: ['胸腔、腹腔无积液、粘连、纤维素性渗出物', '脾脏、肠系膜淋巴结无肠炭疽']
          }, {
              name: '心脏',
              values: ['心包无变性、心包积液、渗出、淤血、出血、坏死等症状','心内膜、心肌、血液凝固状态、二尖瓣及无虎斑心、菜花样赘生物、寄生虫等']
          }, {
              name: '肺脏',
              values: ['肺脏形状、大小、色泽，触检弹性正常', '肺实质无坏死、萎陷、气肿、水肿、淤血、脓肿、实变、结节、纤维素性渗出物等', '支气管淋巴结无出血、淤血、肿胀、坏死等']
          }, {
              name: '肝脏',
              values: ['肝脏形状、大小、色泽，触检弹性正常', '无淤血、肿胀、变性、黄染、坏死、硬化、肿物、结节、纤维素性渗出物、寄生虫等病变', '无出血、淤血、肿胀、坏死等']
          }, {
              name: '脾脏',
              values: ['脾脏形状、大小、色泽，触检弹性正常', '无肿胀、淤血、坏死灶、边缘出血性梗死、被膜隆起及粘连等']
          }, {
              name: '胃和肠',
              values: ['无淤血、出血、坏死、胶冻样渗出物和粘连', '肠系膜淋巴结无淤血、出血、坏死、溃疡等病变', '黏膜无淤血、出血、水肿、坏死、溃疡']
          }],
          quarantineData03: [{
              name: '整体检查',
              values: ['皮肤、皮下组织、脂肪、肌肉、淋巴结、骨骼以及胸腔、腹腔浆膜无淤血、出血、疹块、黄染、脓肿和其他异常']
          }, {
              name: '淋巴结',
              values: ['部底壁皮下、后肢内侧、腹股沟皮下环附近的两侧腹股沟浅淋巴结，检查无淤血、水肿、出血、坏死、增生等病变','腹股沟深淋巴结、髂下淋巴结及髂内淋巴结无异常']
          }, {
              name: '腰肌',
              values: ['荐椎与腰椎结合部两侧肌纤维无猪囊尾蚴']
          }, {
              name: '肾脏',
              values: ['肾脏形状、大小、色泽，触检质地无异常', '无贫血、出血、淤血、肿胀等病变', '切面皮质部无颜色变化、出血及隆起等']
          }, {
              name: '旋毛虫',
              values: ['左右膈脚无异常']
          }]
      }
  },
  mounted () {
    /** 检疫滚动轴样式 */
    $(".quarantine-add-scroll").lzscroll("y", 60, "hover", "mytheme");
  },
  created () {
      
  },
  watch: {
      
  },
  methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
      handleReset() {},
      handleSave() {},
  }
}
</script>

<style lang="sass">
@import './add_quarantine.scss'
</style>



