<template>
  <div class="slaughte-census-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="slaughte-census-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE01D;</i>
                </span>
                <span>屠宰场统计</span>
            </div>
            <div class="content">
                <el-form :model="slaughteForm" :rules="slaughteRules" ref="slaughteForm" label-width="120px">
                  <el-row>
                    <el-col :span="8">
                        <el-form-item label="屠宰场名称">
                            <el-input v-model="slaughteForm.name" clearable placeholder="请输入屠宰场名称"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="屠宰种类">
                            <el-select v-model="slaughteForm.type" clearable placeholder="请选择类型">
                                <el-option label="猪" value="猪"></el-option>
                                <el-option label="牛" value="牛"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="时间范围">
                            <el-date-picker v-model="slaughteForm.date" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" @change="handleCheckDate"></el-date-picker>
                        </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
                <div slot="footer" class="bottom-footer">
                    <el-button size="mini" class="btn-common btn-back" @click="handleReset()">重置</el-button>
                    <el-button size="mini" class="btn-common btn-query" @click="handleCensus()">统计</el-button>
                </div>
                <div class="census-echarts">
                    <el-row type="flex" :gutter="10">
                      <el-col :span="12">
                          <el-card class="box-card">
                            <div class="pie-echarts"></div>
                          </el-card>
                      </el-col>
                      <el-col :span="12">
                          <el-card class="box-card">
                            <div class="slaughte-echarts"></div>
                          </el-card>
                      </el-col>
                    </el-row>
                    <div class="line-charts">
                        <el-card class="box-card">
                            <div class="in-out-echarts"></div>
                        </el-card>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
export default {
  data() {
    return {
      slaughteForm: {
        name: "",
        type: ""
      },
      slaughteRules: {},
      pieCharts: null,
      lineCharts: null,
      slaughteCharts: null,
      /** 流入产出折线图配置 */
      lineOptions: {
        title: {
          text: "屠宰场流入产出对比图",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["流入", "产出"],
          textStyle: {
            color: "#383838"
          },
          top: 40
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: {
          type: "category",
          name: "星期",
          boundaryGap: true,
          data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
          axisLine: {
            lineStyle: {
              color: "#383838"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "流入（万斤）",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          },
          {
            type: "value",
            name: "产出（万斤）",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "流入",
            data: [20, 32, 91, 34, 29, 33, 32],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#0EB206"
              }
            }
          },
          {
            name: "产出",
            data: [24, 28, 45, 95, 24, 68, 56],
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      /** 肉类产出占比饼图配置项 */
      pieOptions: {
        title: {
          text: "屠宰场肉类产出占比图",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["猪", "牛", "羊"]
        },
        series: [
          {
            name: "肉类产出占比",
            type: "pie",
            radius: ["40%", "55%"],
            selectedMode: 'single',
            label: {
              normal: {
                show: false,
                formatter: "{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                shadowBlur: 3,
                rich: {
                  a: {
                    color: "#999",
                    lineHeight: 22,
                    align: "center"
                  },
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 12034, name: "猪", selected: true },
              { value: 8350, name: "牛" },
              { value: 9010, name: "羊" },
            ]
          }
        ]
      },
      /** 屠宰场分布占比饼图配置项 */
      slaughteOptions: {
        title: {
          text: "屠宰场分布统计",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["大兴区", "长安一区", "航天一区", "航天二区", "高新区"]
        },
        series: [
          {
            name: "屠宰场分布",
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            selectedMode: 'single',
            avoidLabelOverlap: false,
            label: {
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "600"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              },
            },
            data: [
              { value: 15, name: "大兴区", selected: true},
              { value: 31, name: "长安一区" },
              { value: 13, name: "航天一区" },
              { value: 9, name: "航天二区" },
              { value: 5, name: "高新区" }
            ]
          }
        ]
      }
    };
  },
  mounted() {
    /** 流入产出折线图 */
    let lineDom = $(".in-out-echarts")[0];
    this.lineCharts = this.$echarts.init(lineDom);
    this.lineCharts.setOption(this.lineOptions);

    /** 肉类分类饼图 */
    let pieDom = $(".pie-echarts")[0];
    this.pieCharts = this.$echarts.init(pieDom);
    this.pieCharts.setOption(this.pieOptions);

    /** 屠宰场分布饼图 */
    let slaughtePieDom = $(".slaughte-echarts")[0];
    this.slaughteCharts = this.$echarts.init(slaughtePieDom);
    this.slaughteCharts.setOption(this.slaughteOptions);
  },
  created() {},
  watch: {},
  methods: {
    handleCheckDate() {},

    /** 统计 */
    handleCensus() {},

    /** 重置 */
    handleReset() {}
  }
};
</script>

<style lang="sass">
@import './slaughte_census.scss'
</style>



