<template>
  <div class="client-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="client-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>客户管理</span>
            </div>
            <div class="content">
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="供应商" name="first">
                        <div class="client-content">
                            <el-form :model="supplierForm" :rules="supplierRules" ref="supplierForm" label-width="120px">
                                <el-row type="flex" :gutter="10">
                                <el-col :span="8">
                                    <el-form-item label="客户名称" prop="userName">
                                        <el-input v-model="supplierForm.userName" clearable placeholder="请输入客户名称"></el-input>
                                    </el-form-item>
                                    <el-form-item class="half-input" label="能繁衍系数">
                                        <el-input v-model="supplierForm.xishuMin" clearable placeholder="请输入系数"></el-input> -
                                        <el-input v-model="supplierForm.xishuMax" clearable placeholder="请输入系数"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="8">
                                    <el-form-item label="客户经理" prop="jingli">
                                        <el-select v-model="supplierForm.jingli" clearable placeholder="请输入客户名称">
                                            <el-option label="李四" value="李四"></el-option>
                                        </el-select>
                                    </el-form-item>
                                    <el-form-item class="half-input" label="存栏量(头)">
                                        <el-input v-model="supplierForm.cunlanMin" clearable placeholder="请输入存栏量"></el-input> -
                                        <el-input v-model="supplierForm.cunlanMax" clearable placeholder="请输入存栏量"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="8">
                                    <el-form-item label="饲养类型" prop="type">
                                        <el-select v-model="supplierForm.type" clearable placeholder="请选择饲养类型">
                                            <el-option label="猪" value="猪"></el-option>
                                            <el-option label="牛" value="牛"></el-option>
                                            <el-option label="羊" value="羊"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                </el-row>
                            </el-form>
                            <div slot="footer" class="bottom-footer">
                                <el-button size="small" class="btn-common btn-back" @click="handleReset()">清空</el-button>
                                <el-button size="small" class="btn-common btn-ok" @click="handleQuery()">查询</el-button>
                            </div>
                            <el-row class="bottom-content" :gutter="10">
                              <el-col :span="18">
                                <el-table class="client-table" :data="supplierData" highlight-current-row>
                                    <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                                    <el-table-column prop="name" label="姓名" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="type" label="饲养类别" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="xishu" label="繁衍系数" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="number" label="存栏量(头)" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="username" label="客户经理" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column label="操作" align="center" width="120">
                                        <template slot-scope="scope">
                                            <el-button type="primary" size="mini" @click="handleUpdateDevice(scope.$index, scope.row)">编辑</el-button>
                                            <el-button type="danger" size="mini" @click="handleDeleteDevice(scope.$index, scope.row)">删除</el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                              </el-col>
                              <el-col :span="6">
                                  <div class="shopping-record">
                                      <div class="shop-title">买入记录：</div>
                                      <div class="shop-conent">
                                        <el-table class="record-table" :data="supplierRecord" highlight-current-row :show-header="false">
                                            <el-table-column prop="time" label="时间" align="center" show-overflow-tooltip></el-table-column>
                                            <el-table-column prop="number" label="数量(头)" align="center" show-overflow-tooltip>
                                                <template slot-scope="scope">
                                                    {{ scope.row.number }} 头
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="type" label="类型" align="center" show-overflow-tooltip></el-table-column>
                                            <el-table-column prop="total" label="总数(公斤)" align="center" show-overflow-tooltip>
                                                <template slot-scope="scope">
                                                    {{ scope.row.total }} 公斤
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                      </div>
                                  </div>
                              </el-col>
                            </el-row>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="销售商" name="second">
                        <div class="client-content">
                            <el-form :model="saleForm" :rules="saleRules" ref="saleForm" label-width="120px">
                                <el-row type="flex" :gutter="10">
                                <el-col :span="8">
                                    <el-form-item label="客户名称" prop="userName">
                                        <el-input v-model="saleForm.userName" clearable placeholder="请输入客户名称"></el-input>
                                    </el-form-item>
                                    <el-form-item class="half-input" label="需求量">
                                        <el-input v-model="saleForm.cunlanMin" clearable placeholder="请输入需求量"></el-input> -
                                        <el-input v-model="saleForm.cunlanMax" clearable placeholder="请输入需求量"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="8">
                                    <el-form-item label="客户经理" prop="jingli">
                                        <el-select v-model="saleForm.jingli" clearable placeholder="请输入客户名称">
                                            <el-option label="李四" value="李四"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="8">
                                    <el-form-item label="饲养类型" prop="type">
                                        <el-select v-model="saleForm.type" clearable placeholder="请选择饲养类型">
                                            <el-option label="猪" value="猪"></el-option>
                                            <el-option label="牛" value="牛"></el-option>
                                            <el-option label="羊" value="羊"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                </el-row>
                            </el-form>
                            <div slot="footer" class="bottom-footer">
                                <el-button size="small" class="btn-common btn-back" @click="handleReset()">清空</el-button>
                                <el-button size="small" class="btn-common btn-ok" @click="handleQuery()">查询</el-button>
                            </div>
                            <el-row class="bottom-content" :gutter="10">
                              <el-col :span="18">
                                <el-table class="client-table" :data="saleData" highlight-current-row>
                                    <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                                    <el-table-column prop="name" label="姓名" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="type" label="饲养类别" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="number" label="存栏量(头)" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="username" label="客户经理" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column label="操作" align="center" width="120">
                                        <template slot-scope="scope">
                                            <el-button type="primary" size="mini" @click="handleUpdateDevice(scope.$index, scope.row)">编辑</el-button>
                                            <el-button type="danger" size="mini" @click="handleDeleteDevice(scope.$index, scope.row)">删除</el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                              </el-col>
                              <el-col :span="6">
                                  <div class="shopping-record">
                                      <div class="shop-title">销售记录：</div>
                                      <div class="shop-conent">
                                        <el-table class="record-table" :data="saleRecord" highlight-current-row :show-header="false">
                                            <el-table-column prop="time" label="时间" align="center" show-overflow-tooltip></el-table-column>
                                            <el-table-column prop="number" label="数量(头)" align="center" show-overflow-tooltip>
                                                <template slot-scope="scope">
                                                    {{ scope.row.number }} 头
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="type" label="类型" align="center" show-overflow-tooltip></el-table-column>
                                            <el-table-column prop="total" label="总数(公斤)" align="center" show-overflow-tooltip>
                                                <template slot-scope="scope">
                                                    {{ scope.row.total }} 公斤
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                      </div>
                                  </div>
                              </el-col>
                            </el-row>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
        activeName: 'first',
        supplierForm: {

        },
        supplierRules: {

        },

        supplierData: [{
            name: '张三',
            type: '牛',
            xishu: '1.5',
            number: '2513',
            username: '李四'
        }],

        supplierRecord: [{
            time: '2018-2-4',
            number: '251',
            type: '猪',
            total: '3250'
        }],

        saleForm: {

        },
        saleRules: {

        },
        saleData: [{
            name: '张三',
            type: '牛',
            number: '2513',
            username: '李四'
        }],

        saleRecord: [{
            time: '2018-2-4',
            number: '251',
            type: '猪',
            total: '3250'
        }],

    };
  },

  mounted() {},

  created() {},

  watch: {},

  methods: {
      handleClick() {},

      /** 清空 */
      handleReset() {},

      /** 查询 */
      handleQuery() {},
  }
};
</script>

<style lang="sass">
@import './client_manage.scss'
</style>



