<template>
  <div class="manage-home-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="manage-home-content uk-flex uk-flex-column uk-flex-nowrap">
            <el-row class="top-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="one-card-item" v-for="(itemMenu, indexMenu) in outInData" :key="indexMenu">
                        <div class="in-out-title">
                            <span class="title">{{ indexMenu == 0 ? '今日流入' : '今日流出' }}</span>
                            <div class="in-out-slick">
                                <div class="slick-item" v-for="(item, index) in itemMenu" :key="index">
                                    <img :src="item.imgSrc">
                                    <span :title="'共计 ' + item.total + ' 公斤'">{{ item.type }} 共计 {{ item.total }} 公斤</span>
                                    <span>周同比</span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                </div>
                            </div>
                        </div>
                        <div class="one-card-content">
                            <div class="one-content-item" v-for="(item, index) in itemMenu" :key="index">
                                <div class="title">{{ item.type }}流入</div>
                                <div class="mid">
                                    <img :src="item.imgSrc">
                                    <span :title="'共计 ' + item.total + ' 公斤'">{{ item.type }} 共计 {{ item.total }} 公斤</span>
                                </div>
                                <div class="bottom">
                                    <span>周同比</span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                    <span>{{ item.type }} {{item.number}} 头</span>
                                </div>
                            </div>
                        </div>
                      </div>
                  </div>
              </el-col>
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title">流入产出趋势</div>
                      <div class="two-btn">
                        <el-button size="mini" type="primary">今日</el-button>
                        <el-button size="mini" type="primary">本周</el-button>
                        <el-button size="mini" type="primary">本月</el-button>
                        <el-button size="mini" type="primary">全年</el-button>
                        <el-date-picker v-model="inOutDate" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" @change="handleCheckDate"></el-date-picker>
                      </div>
                      <div class="in-out-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="8">
                  <div class="card">
                      <div class="title">肉类占比图</div>
                      <div class="three-btn">
                        <el-button size="mini" type="primary">全部肉类</el-button>
                        <el-button size="mini" type="primary">大类</el-button>
                        <el-button size="mini" type="primary">小类</el-button>
                      </div>
                      <div class="pie-echarts"></div>
                  </div>
              </el-col>
            </el-row>
            <el-row class="bittom-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title">屠宰场分布占比图</div>
                      <div class="slaughte-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="16">
                  <div class="card">
                      <div class="title">屠宰场分布地区</div>
                      <div class="slaughte-map">
                        <vMap ref="map"></vMap>
                      </div>
                  </div>
              </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
/** 地图 */
import vMap from "../../components/map/MapComponent.vue";
// 轮播
import Slick from "vue-slick";

export default {
  components: {
    vMap,
    Slick
  },
  data() {
    return {
      lineCharts: null,
      pieCharts: null,
      slaughteCharts: null,
      mapApi: null,
      outInData: [
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "猪",
            total: "25420.8",
            trend: "el-icon-caret-top",
            number: "104",
            percent: "12.5"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "牛",
            total: "1250.3",
            trend: "el-icon-caret-bottom",
            number: "56",
            percent: "5.9"
          },
          {
            imgSrc: require("../../assets/images/home/yang.png"),
            type: "羊",
            total: "8652.9",
            trend: "el-icon-caret-top",
            number: "238",
            percent: "15.9"
          }
        ],
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "猪",
            total: "21001.3",
            trend: "el-icon-caret-top",
            number: "86",
            percent: "9.4"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "牛",
            total: "19502.5",
            trend: "el-icon-caret-bottom",
            number: "78",
            percent: "3.1"
          }
        ]
      ],
      /** 流入产出折线图配置 */
      lineOptions: {
        title: {
          text: "流入产出对比图",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["流入", "产出"],
          textStyle: {
            color: "#383838"
          },
          top: 40
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: {
          type: "category",
          name: "星期",
          boundaryGap: true,
          data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
          axisLine: {
            lineStyle: {
              color: "#383838"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "流入（万斤）",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          },
          {
            type: "value",
            name: "产出（万斤）",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "流入",
            data: [20, 32, 51, 34, 29, 33, 32],
            type: "line",
            smooth: true,
            areaStyle: {
                color: {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                        offset: 0, 
                        color: '#0EB206'
                    }, {
                        offset: 1, 
                        color: '#FEF6F3'
                    }],
                    globalCoord: false
                }
            },
            itemStyle: {
              normal: {
                color: "#0EB206"
              }
            }
          },
          {
            name: "产出",
            data: [24, 35, 55, 45, 32, 44, 42],
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            areaStyle: {
                color: {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                        offset: 0, 
                        color: '#f72020'
                    }, {
                        offset: 1, 
                        color: '#FEF6F3'
                    }],
                    globalCoord: false
                }
            },
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      inOutDate: "",
      /** 肉类分类占比饼图配置项 */
      pieOptions: {
        title: {
          text: "肉类分类占比统计图",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["猪", "牛", "羊"]
        },
        series: [
          {
            name: "肉类占比",
            type: "pie",
            radius: ["40%", "55%"],
            selectedMode: 'single',
            label: {
              normal: {
                show: false,
                formatter: "{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                shadowBlur: 3,
                rich: {
                  a: {
                    color: "#999",
                    lineHeight: 22,
                    align: "center"
                  },
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 12034, name: "猪", selected: true },
              { value: 8350, name: "牛" },
              { value: 9010, name: "羊" },
            ]
          }
        ]
      },
      /** 屠宰场分布占比饼图配置项 */
      slaughteOptions: {
        title: {
          text: "屠宰场分布统计",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["大兴区", "长安一区", "航天一区", "航天二区", "高新区"]
        },
        series: [
          {
            name: "屠宰场分布",
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            avoidLabelOverlap: false,
            selectedMode: 'single',
            label: {
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 15, name: "大兴区", selected: true },
              { value: 31, name: "长安一区" },
              { value: 13, name: "航天一区" },
              { value: 9, name: "航天二区" },
              { value: 5, name: "高新区" }
            ]
          }
        ]
      },
      slaughteListData: [
        {
          id: "1",
          name: "城北屠宰场",
          lon: "108.936779",
          lat: "34.312734",
          type: "redHot"
        },
        {
          id: "2",
          name: "城北屠宰场分厂",
          lon: "108.956779",
          lat: "34.342734",
          type: "redHot"
        },
        {
          id: "3",
          name: "城东屠宰场",
          lon: "108.996779",
          lat: "34.322734",
          type: "redHot"
        },
        {
          id: "4",
          name: "城南屠宰场",
          lon: "108.956779",
          lat: "34.272734",
          type: "redHot"
        },
      ]
    };
  },
  mounted() {
    /** 流入流出滚动轴样式 */
    $(".one-card-content").lzscroll("y", 60, "hover", "mytheme");

    /** 流入产出折线图 */
    let lineDom = $(".in-out-echarts")[0];
    this.lineCharts = this.$echarts.init(lineDom);
    this.lineCharts.setOption(this.lineOptions);

    /** 肉类分类饼图 */
    let pieDom = $(".pie-echarts")[0];
    this.pieCharts = this.$echarts.init(pieDom);
    this.pieCharts.setOption(this.pieOptions);

    /** 屠宰场分布饼图 */
    let slaughtePieDom = $(".slaughte-echarts")[0];
    this.slaughteCharts = this.$echarts.init(slaughtePieDom);
    this.slaughteCharts.setOption(this.slaughteOptions);

    // 初始化地图操作接口
    this.mapApi = this.$refs["map"].mapApi;

    $(".in-out-slick").slick({
      slidesToShow: 1,
      autoplaySpeed: '1000',
      arrows: false,
      vertical: true,
      autoplay: true,
      infinite: true
    });
  },
  created() {
    this.$nextTick(() => {
      this.mapApi.markerApi.removeMarkerByType("redHot");
      /** 在地图上创建点 */
      this.mapApi.markerApi.addMarkers(this.slaughteListData);
    });
  },
  watch: {},
  methods: {
    /** 选中时间获取流入产出趋势数据 */
    handleCheckDate() {}
  }
};
</script>

<style lang="sass">
@import './manage_home.scss'

@import '../../../node_modules/slick-carousel/slick/slick.css';
</style>



