<template>
  <div class="quarantine-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="quarantine-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>检疫记录</span>
            </div>
            <div class="content">
                <div class="demo-input-suffix">
                    <span>检疫人员：</span>
                    <el-select v-model="quarantineUser" clearable placeholder="请选择检疫人员">
                        <el-option label="张三" value="张三"></el-option>
                        <el-option label="李四" value="李四"></el-option>
                    </el-select>
                    <span style="margin-left: 10px;">是否通过：</span>
                    <el-radio v-model="isAdopt" label="1">是</el-radio>
                    <el-radio v-model="isAdopt" label="0">否</el-radio>
                    <el-button size="mini" type="primary" class="btn-query">查询</el-button>
                </div>
                <el-table class="quarantine-table" :data="quarantineData" highlight-current-row height="calc(100% - 79px)">
                    <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                    <el-table-column prop="time" label="检疫时间" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="user" label="检疫员" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="code" label="入场单号" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="result" label="检疫结果" align="center" show-overflow-tooltip width="100px">
                        <template slot-scope="scope">
                            <el-tag :type="scope.row.result == 1 ? 'success' : 'danger'"></el-tag>
                            {{scope.row.result == 1 ? '通过' : '未通过'}}
                        </template>
                    </el-table-column>
                </el-table>
                <div class="main-page">
                    <el-pagination @size-change="handleQuarantineSize" @current-change="handleQuarantineCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="quarantineTotal">
                    </el-pagination>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./quarantine_service.js";
export default {
  data() {
    return {
      /** 设备表格每页展示的数据量 */
      pageSize: 15,
      /** 设备表格总数据量 */
      quarantineTotal: 2,
      /** 当前是第几页 */
      page: 1,
      quarantineUser: "",
      isAdopt: "1",
      quarantineData: [
        {
          time: "2018-5-4",
          user: "张三",
          code: "I52654258569",
          result: "1"
        },
        {
          time: "2018-5-10",
          user: "李思思",
          code: "I47812352425",
          result: "0"
        }
      ]
    };
  },
  mounted() {},
  created() {},
  watch: {},
  methods: {
    /** 表格修改每页数量后执行的函数 */
    handleQuarantineSize(val) {
      this.quarantineData = [];
      this.pageSize = val;
      this.quarantineDataQuery(1);
    },

    /** 表格页码改变后执行的函数 */
    handleQuarantineCurrent(val) {
      this.quarantineData = [];
      this.page = val;
      this.quarantineDataQuery();
    },

    /** 查询检疫人员列表 */
    quarantineDataQuery(pg) {}
  }
};
</script>

<style lang="sass">
@import './quarantine_record.scss'
</style>



