<template>
  <div class="car-monitor-page uk-flex uk-flex-row uk-flex-nowrap">
      <div class="car-monitor-left uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>车辆列表</span>
            </div>
            <div class="left-content">
                <el-card shadow="hover" v-for="(item, index) in carListData" :key="index">
                    <el-form label-position='left' :model="item" label-width="100px">
                      <el-form-item label="车辆牌照">
                          <el-tag type="warning">{{item.name}}</el-tag>
                      </el-form-item>
                      <el-form-item label="驾驶员姓名">
                          <span>{{item.userName}}</span>
                      </el-form-item>
                      <el-form-item label="驾驶员电话">
                          <span>{{item.phone}}</span>
                      </el-form-item>
                    </el-form>
                </el-card>
            </div>
        </div>
        <div class="car-monitor-right uk-flex-item-none">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">map</i>
                </span>
                <span>车辆位置</span>
            </div>
            <div class="right-content">
                <vMap ref="map"></vMap>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./real_time_service.js";
/** 地图 */
import vMap from "../../components/map/MapComponent.vue";

export default {
  components: {
    vMap
  },
  data() {
    return {
      mapApi: null,
      carListData: [
        {
          id: "1",
          name: "陕A23454",
          userName: '张三',
          phone: '13526425869',
          lon: "108.936779",
          lat: "34.312734",
          type: "redHot"
        },
        {
          id: "2",
          name: "陕A2O980",
          userName: '李四',
          phone: '13654258459',
          lon: "108.956779",
          lat: "34.342734",
          type: "redHot"
        },
        {
          id: "3",
          name: "陕AK8973",
          userName: '王五',
          phone: '13945241256',
          lon: "108.996779",
          lat: "34.322734",
          type: "redHot"
        },
        {
          id: "4",
          name: "陕AW8794",
          userName: '李白',
          phone: '13425625894',
          lon: "108.956779",
          lat: "34.272734",
          type: "redHot"
        }
      ]
    };
  },

  mounted() {
    // 初始化地图操作接口
    this.mapApi = this.$refs["map"].mapApi;

    /** 车辆列表滚动轴样式 */
    $(".left-content").lzscroll("y", 60, "hover", "mytheme");
  },

  created() {
    this.$nextTick(() => {
      this.mapApi.markerApi.removeMarkerByType("redHot");
      /** 在地图上创建点 */
      this.mapApi.markerApi.addMarkers(this.carListData);
    });
  },

  watch: {},

  methods: {}
};
</script>

<style lang="sass">
@import './car_monitor.scss'
</style>



