<template>
  <div class="outIn-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="outIn-content uk-flex uk-flex-column uk-flex-nowrap">
            <el-row class="outIn-card" type="flex" :gutter="5">
              <el-col :span="18">
                  <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>入场</span>
                    </div>
                    <div class="in-content">
                        <el-form class="in-form form-scroll" :model="inForm" :rules="inRules" ref="inForm" label-width="100px">
                            <el-row type="flex" :gutter="5">
                            <el-col :span="12">
                                <el-form-item label="入场单号">
                                    <el-input v-model="inForm.inCode" clearable placeholder="请输入入场单号"></el-input>
                                </el-form-item>
                                <el-form-item label="类型">
                                    <el-select v-model="inForm.type" clearable placeholder="请选择类型">
                                        <el-option label="猪" value="猪"></el-option>
                                        <el-option label="牛" value="牛"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="客户">
                                    <el-select v-model="inForm.user" clearable placeholder="请选择客户">
                                        <el-option label="张三" value="张三"></el-option>
                                        <el-option label="李四" value="李四"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="数量(头)">
                                    <el-input v-model="inForm.number" clearable placeholder="请输入数量"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="是否检疫">
                                    <el-radio v-model="inForm.isQuarantine" label="1">是</el-radio>
                                    <el-radio v-model="inForm.isQuarantine" label="0">否</el-radio>
                                </el-form-item>
                                <el-form-item label="检疫通过">
                                    <el-radio v-model="inForm.adopt" label="1">通过</el-radio>
                                    <el-radio v-model="inForm.adopt" label="0">未通过</el-radio>
                                </el-form-item>
                                <el-form-item label="检疫员">
                                    <el-select v-model="inForm.Quarantiner" clearable placeholder="请选择检疫员">
                                        <el-option label="张三" value="张三"></el-option>
                                        <el-option label="李四" value="李四"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="记重(公斤)">
                                    <el-input v-model="inForm.weight" clearable placeholder="请输入重量"></el-input>
                                </el-form-item>
                            </el-col>
                            </el-row>
                            <el-form-item label="备注">
                                <el-input v-model="inForm.remark" type="textarea" :rows="3" clearable placeholder="请输入备注"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="bottom-footer">
                            <el-button size="small" class="btn-common btn-back" @click="handleInReset()">清空</el-button>
                            <el-button size="small" class="btn-common btn-ok" @click="handleInSave()">入场</el-button>
                        </div>
                    </div>
                  </el-card>
                  <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>出场</span>
                    </div>
                    <div class="out-content">
                        <el-form class="out-form form-scroll" :model="outForm" :rules="outRules" ref="outForm" label-width="100px">
                            <el-row type="flex" :gutter="5">
                            <el-col :span="12">
                                <el-form-item label="出场单号">
                                    <el-input v-model="outForm.inCode" clearable placeholder="请输入出场单号"></el-input>
                                </el-form-item>
                                <el-form-item label="类型">
                                    <el-select v-model="outForm.type" clearable placeholder="请选择类型">
                                        <el-option label="猪" value="猪"></el-option>
                                        <el-option label="牛" value="牛"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="记重(公斤)">
                                    <el-input v-model="outForm.weight" clearable placeholder="请输入重量"></el-input>
                                </el-form-item>
                                <el-form-item label="客户">
                                    <el-select v-model="outForm.user" clearable placeholder="请选择客户">
                                        <el-option label="张三" value="张三"></el-option>
                                        <el-option label="李四" value="李四"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            </el-row>
                            <el-form-item label="备注">
                                <el-input v-model="outForm.remark" type="textarea" :rows="4" clearable placeholder="请输入备注"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="bottom-footer">
                            <el-button size="small" class="btn-common btn-back" @click="handleOutReset()">清空</el-button>
                            <el-button size="small" class="btn-common btn-ok" @click="handleOutSave()">出场</el-button>
                        </div>
                    </div>
                  </el-card>
                  
              </el-col>
              <el-col :span="6">
                  <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>入场纪录</span>
                    </div>
                    <el-table class="record-table" :data="inRecord" highlight-current-row :show-header="false">
                        <el-table-column prop="pic" label="pic" align="center" show-overflow-tooltip width="50px">
                            <template slot-scope="scope">
                                <img :src="scope.row.pic">
                            </template>
                        </el-table-column>
                        <el-table-column prop="time" label="时间" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="type" label="类型" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="number" label="数量(头)" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <el-tag type="success" close-transition>
                                    {{ scope.row.number }} 头
                                </el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="total" label="总数(公斤)" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <span style="color: #ff0000">{{ scope.row.total }}</span>公斤
                            </template>
                        </el-table-column>
                    </el-table>
                  </el-card>
                  <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>出场记录</span>
                    </div>
                    <el-table class="record-table" :data="outRecord" highlight-current-row :show-header="false">
                        <el-table-column prop="pic" label="pic" align="center" show-overflow-tooltip width="50px">
                            <template slot-scope="scope">
                                <img :src="scope.row.pic">
                            </template>
                        </el-table-column>
                        <el-table-column prop="time" label="时间" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="type" label="类型" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="total" label="总数(公斤)" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <span style="color: #ff0000">{{ scope.row.total }}</span>公斤
                            </template>
                        </el-table-column>
                    </el-table>
                  </el-card>
              </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
import serivce from "./outIn_service.js";
export default {
  data() {
    return {
        inForm: {
            isQuarantine: '1',
            adopt: '1',
        },
        inRules: {},

        outForm: {

        },
        outRules: {

        },

        inRecord: [{
            pic: require('../../assets/images/home/zhu.png'),
            time: '2018-4-28',
            type: '猪',
            number: '254',
            total: '2520'
        }],

        outRecord: [{
            pic: require('../../assets/images/home/niu.png'),
            time: '2018-5-3',
            type: '牛',
            total: '5486'
        }]
    };
  },

  mounted() {
    /** 表单滚动轴样式 */
    $(".form-scroll").lzscroll("y", 60, "hover", "mytheme");
  },

  created() {},

  watch: {},

  methods: {
      /** 清空入场表单 */
      handleInReset() {

      },
      /** 入场保存 */
      handleInSave () {

      },

      /** 清空出场表单 */
      handleInReset() {

      },
      /** 出场保存 */
      handleInSave () {

      }
  }
};
</script>

<style lang="sass">
@import './outIn_manage.scss'
</style>



