<template>
    <div>
        <el-row :gutter="10" class="org-page">
            <el-col :span="6" class="content-col">
                <div class="org-list">
                    <div class="mainbar-title">
                        <span>
                            <i class="md-icon material-icons">&#xE01D;</i>
                        </span>
                        <span>组织机构列表</span>
                        <span style="float: right;">
                            <i class="md-icon material-icons" title="刷新">&#xE863;</i>
                        </span>
                    </div>
                    <div class="list-content">
                        <div class="tree-filter">
                            <el-input icon="search" placeholder="名称搜索" v-model="filterText"></el-input>
                        </div>
                        <el-tree class="filter-tree" :data="dataTree" default-expand-all highlight-current :filter-node-method="filterNode" ref="orgTree" :expand-on-click-node="false" :props="{children: 'children', label: 'name'}">
                        </el-tree>
                    </div>
                </div>
            </el-col>
            <el-col :span="10" class="content-col">
                <div class="child-user">
                    <div class="mainbar-title">
                        <span>下属人员信息</span>
                        <span style="float: right;">
                            <i class="md-icon material-icons" title="新增" @click="handleUserAdd">&#xE145;</i>
                        </span>
                    </div>
                    <div class="child-user-content">
                        <span class="child-user-name">姓名：</span>
                        <el-input class="child-user-input" v-model="filterUser" placeholder="请输入姓名"></el-input>
                        <el-table class="child-user-table" :data="userFilterData" highlight-current-row>
                            <el-table-column prop="userName" label="姓名" show-overflow-tooltip align="center"></el-table-column>
                            <el-table-column prop="sex" label="性别" width="80" align="center">
                                <template slot-scope="scope">
                                    <el-tag :type="scope.row.sex === 1 ? 'primary' : 'danger'" close-transition>
                                        {{scope.row.sex=== 1 ? '男' : '女'}}
                                    </el-tag>
								</template>
                            </el-table-column>
                            <el-table-column prop="phone" label="电话" show-overflow-tooltip align="center"></el-table-column>
                            <el-table-column prop="isDeptAdmin" label="管理员" align="center">
                                <template slot-scope="scope">
                                    <div class="status-tag">
                                        <el-tag :type="scope.row.isDeptAdmin === 1 ? 'success' : 'danger'" close-transition></el-tag>
                                        <span>{{scope.row.isDeptAdmin=== 1 ? '是' : '否'}}</span>
                                    </div>
								</template>
                            </el-table-column>
                            <el-table-column label="操作" width="170" align="center">
                                <template slot-scope="scope">
                                    <i class="md-icon edit-icon-color el-icon-edit" @click="handleUserEdit(scope.$index, scope.row)"></i>
                                    <i class="md-icon user-icon-color material-icons" @click="handleRoleToUser(scope.$index, scope.row)">&#xE8E8;</i>
                                    <i class="md-icon is-admin-icon-color material-icons" @click="handleIsAdmin(scope.$index, scope.row)">&#xE8A6;</i>
                                    <i class="md-icon delete-icon-color el-icon-delete" @click="handleUserDelete(scope.$index, scope.row)"></i>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                </div>
                <div class="child-role">
                    <div class="mainbar-title">
                        <span>下属角色信息</span>
                        <span style="float: right;">
                            <i class="md-icon material-icons" title="新增" @click="handleRoleAdd">&#xE145;</i>
                        </span>
                    </div>
                    <div class="child-role-content">
                        <span class="child-role-name">角色：</span>
                        <el-input class="child-role-input" v-model="filterRole" placeholder="请输入角色"></el-input>
                        <el-table class="child-role-table" :data="roleFilterData">
                            <el-table-column prop="id" label="序号" width="70" show-overflow-tooltip align="center"></el-table-column>
                            <el-table-column prop="name" label="角色名称" show-overflow-tooltip align="center"></el-table-column>
                            <el-table-column prop="type" label="角色类型" show-overflow-tooltip align="center">
                                <template slot-scope="scope">
                                    <el-tag :type="scope.row.type === 0 ? 'primary' : (scope.row.type === 1 ? 'danger' : 'warning')" close-transition>
                                        {{scope.row.type === 0 ? '普通角色' : (scope.row.type === 1 ? '系统管理员角色' : '部门管理员角色')}}
                                    </el-tag>
								</template>
                            </el-table-column>
                            <el-table-column label="操作" width="130" align="center">
                                <template slot-scope="scope">
                                    <i class="md-icon edit-icon-color el-icon-edit" @click="handleRoleEdit(scope.$index, scope.row)"></i>
                                    <i class="md-icon power-icon-color material-icons" @click="handleUserToRole(scope.$index, scope.row)">&#xE8E8;</i>
                                    <i class="md-icon delete-icon-color el-icon-delete" @click="handleRoleDelete(scope.$index, scope.row)"></i>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                </div>
            </el-col>
            <el-col :span="8" class="content-col">
                <div class="org-info">
                    <div class="mainbar-title">
                        <span>
                            <i class="md-icon material-icons">&#xE85D;</i>
                        </span>
                        <span>组织机构信息</span>
                        <span style="float: right;">
                            <i class="md-icon material-icons" title="删除" @click="handleOrgDelete">&#xE92B;</i>
                        </span>
                    </div>
                    <div class="org-info-edit">
                        <el-form :model="orgInfo" :rules="rules" ref="orgInfo" label-width="100px">
                            <el-row type="flex" justify="space-around">
                                <el-col :span="12">
                                    <el-form-item label="机构名称" prop="name">
                                        <el-input v-model="orgInfo.name" placeholder="请输入机构名称"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="简称" prop="shortName">
                                        <el-input v-model="orgInfo.shortName" placeholder="请输入内容"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                            <el-row type="flex" justify="space-around">
                                <el-col :span="12">
                                    <el-form-item label="上级部门">
                                        <el-input v-model="orgInfo.parentName" disabled placeholder="请输入上级部门"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="分类">
                                        <el-select v-model="orgInfo.type" placeholder="请选择分类">
                                            <el-option label="普通部门" :value="0">普通部门</el-option>
                                            <el-option label="管理部门" :value="1">管理部门</el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                            <el-row type="flex" justify="space-around">
                                <el-col :span="12">
                                    <el-form-item label="排序号" prop="orderBy">
                                        <el-input v-model="orgInfo.orderBy" placeholder="请输入排序号"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="状态">
                                        <el-radio-group v-model="orgInfo.status">
                                            <el-radio :label="1" :value="1">启用</el-radio>
                                            <el-radio :label="0" :value="0">锁定</el-radio>
                                        </el-radio-group>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </el-form>
                        <div class="org-info-btn">
                            <el-button size="small" class="btn-common btn-cancel" @click="resetForm">重置</el-button>
                            <el-button size="small" class="btn-common btn-ok" @click="submitForm('orgInfo')">保存</el-button>
                        </div>
                    </div>
                    <div class="org-child-info">
                        <div class="org-child-filter">
                            <span class="org-filter-title">下属机构</span>
                            <span class="add-child-org"><i class="md-icon material-icons" title="新增" @click="handleOrgAdd">&#xE145;</i></span>
                            <el-input class="org-filter-input" v-model="filterOrg" placeholder="请输入名称"></el-input>
                            <span class="org-filter-label" style="float:right;">机构名称：</span>
                        </div>
                        <el-table class="org-table" :data="tablePageData" stripe>
                            <el-table-column prop="id" label="序号" width="70" align="center"></el-table-column>
                            <el-table-column prop="name" label="名称" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="shortName" label="简称" show-overflow-tooltip></el-table-column>
                            <el-table-column label="操作" width="220" align="center">
                                <template slot-scope="scope">
                                    <el-button class="but-observe" size="mini" @click="handleChildOrgObserve(scope.$index, scope.row)">查看</el-button>
                                    <el-button type="primary" size="mini" @click="handleChildOrgEdit(scope.$index, scope.row)">编辑</el-button>
                                    <el-button type="danger" size="mini" @click="handleChildOrgDelete(scope.$index, scope.row)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="pages">
                            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[15, 20, 25, 30]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="allTotal">
                            </el-pagination>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!-- <div class="search-user" @click="handleSearchUser">
            <i class="el-icon-search"></i>
        </div> -->
        <!-- <vOrgDialog ref="vOrgDialog"></vOrgDialog>
        <vUserDialog ref="vUserDialog"></vUserDialog>
        <vRoleDialog ref="vRoleDialog"></vRoleDialog>

        <vChildOrgDialog ref="vChildOrgDialog"></vChildOrgDialog>
        <vUpdateUserDialog ref="vUpdateUserDialog"></vUpdateUserDialog>
        <vUpdateRoleDialog ref="vUpdateRoleDialog"></vUpdateRoleDialog>
        <vRoleToUserDialog ref="vRoleToUserDialog"></vRoleToUserDialog>
        <vUserToRoleDialog ref="vUserToRoleDialog"></vUserToRoleDialog>

        <vSearchDialog ref="vSearchDialog"></vSearchDialog> -->
    </div>
</template>

<script>
import service from "./scripts/org_service.js";
// /** 新增部门组织机构 */
// import vOrgDialog from "./OrgDialog.vue";
// /** 新增用户弹出框 */
// import vUserDialog from "./UserDialog.vue";
// /** 新增角色弹出框 */
// import vRoleDialog from "./RoleDialog.vue";

// /** 查看下属部门机构信息 */
// import vChildOrgDialog from "./ChildOrgDialog.vue";
// /** 修改部门下属人员信息 */
// import vUpdateUserDialog from "./UpdateUserDialog.vue";
// /** 修改部门下属角色信息 */
// import vUpdateRoleDialog from "./UpdateRoleDialog.vue";
// /** 部门下属用户添加角色 */
// import vRoleToUserDialog from "./RoleToUserDialog.vue";
// /** 部门下属角色添加用户及授权 */
// import vUserToRoleDialog from "./UserToRoleDialog.vue";

// /** 根据用户名称或联系电话查询结果 */
// import vSearchDialog from "./SearchDialog.vue";

export default {
//   components: {
//     vOrgDialog,
//     vUserDialog,
//     vRoleDialog,
//     vChildOrgDialog,
//     vUpdateUserDialog,
//     vUpdateRoleDialog,
//     vRoleToUserDialog,
//     vUserToRoleDialog,
//     vSearchDialog
//   },
  data() {
    /** 验证排序号 */
    let checkorderById = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("排序号不能为空"));
      }
      if (isNaN(Number(value))) {
        callback(new Error("请输入数字值"));
      } else {
        callback();
      }
    };
    return {
      filterText: "",
      dataTree: [{
          id: 1,
          name: '西安市',
          children: [{
              id: 2,
              name: '长安区'
            }, {
              id: 3,
              name: '未央区'
            }, {
              id: 4,
              name: '雁塔区'
            }]
        }],
      /** 组织机构表单验证 */
      rules: {
        name: [
          { required: true, message: "请输入机构名称", trigger: "blur, change" },
          {
            min: 1,
            max: 15,
            message: "长度在 1 到 15 个字符",
            trigger: "blur, change"
          }
        ],
        shortName: [
          { required: true, message: "请输入机构简称", trigger: "blur, change" },
          {
            min: 1,
            max: 10,
            message: "长度在 1 到 10 个字符",
            trigger: "blur, change"
          }
        ],
        orderBy: [
          { required: true, message: "请输入排序号", trigger: "blur" },
          { validator: checkorderById, trigger: "blur, change" }
        ]
      },
      /** 未整理的所有部门 */
      orgAllList: [],
      /** 所有的下属机构信息 */
      tableAllData: [],
      /** 每一页下属机构信息 */
      tablePageData: [{
          id: '1',
          name: '办公室',
          shortName: '办公室'
      }],
      /** 每页数据个数 */
      pageSize: 15,
      /** 表格数据总数 */
      allTotal: 0,
      /** 组织机构信息 */
      orgInfo: {},
      /** 下属机构过滤 */
      filterOrg: "",
      /** 人员过滤 */
      filterUser: "",
      /** 角色过滤 */
      filterRole: "",
      /** 下属人员表格数据 */
      userTableData: [],
      /** 下属人员过滤后的数据 */
      userFilterData: [{
          userName: '张三',
          sex: 1,
          phone: '13254258568',
          isDeptAdmin: 1,
      }],
      /** 下属角色表格数据 */
      roleTableData: [],
      /** 下属角色过滤后数据 */
      roleFilterData: [{
          id:'1',
          name: '系统管理员角色',
          type: 1
      }],
      /** 下属人员滚动轴 */
      userLzscroll: null,
      /** 部门树滚动轴 */
      orgLzscroll: null
    };
  },

  watch: {
    /** 树节点搜索 */
    filterText(val) {
      this.$refs.orgTree.filter(val);
    },
    /** 获取表格的所有数据 */
    tableAllData() {
      this.$data.allTotal =
        this.$data.tableAllData && this.$data.tableAllData.length;
      this.$data.tablePageData = _.chunk(
        _.cloneDeep(this.$data.tableAllData),
        this.$data.pageSize
      )[0];
    },
    /** 所有下属人员表格数据变化 */
    userTableData() {
      this.userFilterData = _.cloneDeep(this.userTableData);
    },
    /** 所有角色表格数据变化 */
    roleTableData() {
      this.roleFilterData = _.cloneDeep(this.roleTableData);
    },
    /** 下属机构表格数据过滤 */
    filterOrg(newValue) {
      if (newValue != "") {
        let data = _.filter(_.cloneDeep(this.$data.tableAllData), function(
          item
        ) {
          if (item.name.indexOf(newValue) >= 0) {
            return item;
          }
        });
        this.$data.allTotal = data.length;
        this.$data.tablePageData = data;
      } else {
        this.$data.allTotal = this.$data.tableAllData.length;
        this.$data.tablePageData = _.chunk(
          _.cloneDeep(this.$data.tableAllData),
          5
        )[0];
      }
    },
    /** 下属人员表格过滤 */
    filterUser(newValue) {
      if (newValue != "") {
        let data = _.filter(this.$data.userTableData, function(item) {
          if (item.userName.indexOf(newValue) >= 0) {
            return item;
          }
        });
        this.$data.userFilterData = data;
      } else {
        this.$data.userFilterData = this.$data.userTableData;
      }
    },
    /** 下属角色表格过滤 */
    filterRole(newValue) {
      if (newValue != "") {
        let data = _.filter(this.$data.roleTableData, function(item) {
          if (item.name.indexOf(newValue) >= 0) {
            return item;
          }
        });
        this.$data.roleFilterData = data;
      } else {
        this.$data.roleFilterData = this.$data.roleTableData;
      }
    }
  },

  created() {
    // /** 初始化左侧树数据 */
    // this.initOrgTree();

    this.$bus.$on("initOrgTree", target => {
      this.initOrgTree();
    });

    /** 新增部门自动刷新部门树 */
    this.$bus.$on("refreshOrgTree", target => {
      this.handleRefresh(true);
    });
    /** 新增用户自动刷新当前部门下用户信息 */
    this.$bus.$on("addUserRefreshOrg", target => {
      this.handleNodeClick(this.orgInfo);
    });
    /** 新增角色自动刷新当前部门下角色信息 */
    this.$bus.$on("addRoleRefreshOrg", target => {
      this.handleNodeClick(this.orgInfo);
    });
    /** 全局搜索用户部门列表回调 */
    this.$bus.$on("searchUserFromOrg", target => {
      this.handleNodeClick(target.deptList[0], target);
    });
  },

  mounted() {
    /** 部门树滚动轴样式 */
    this.orgLzscroll = $(".filter-tree").lzscroll("xy", 60, "hover", "mytheme");
    /** 下属部门表格滚动轴样式 */
    $(".org-table .el-table__body-wrapper").lzscroll(
      "xy",
      60,
      "hover",
      "mytheme"
    );
    /** 下属用户表格滚动轴样式 */
    this.userLzscroll = $(".child-user-table .el-table__body-wrapper").lzscroll(
      "xy",
      60,
      "hover",
      "mytheme"
    );
    /** 下属角色表格滚动轴样式 */
    $(".child-role-table .el-table__body-wrapper").lzscroll(
      "xy",
      60,
      "hover",
      "mytheme"
    );
  },

  methods: {
    /** 部门树数据组装成tree所需格式 */
    orgTreeData(data, idStr, pidStr, chindrenStr) {
      let dataTree = [],
        hash = {},
        id = idStr,
        pid = pidStr,
        children = chindrenStr,
        len = data.length;
      for (let i = 0; i < len; i++) {
        hash[data[i][id]] = data[i];
      }
      for (let j = 0; j < len; j++) {
        let aVal = data[j],
          hashVP = hash[aVal[pid]];
        if (hashVP) {
          !hashVP[children] && (hashVP[children] = []);
          hashVP[children].push(aVal);
        } else {
          dataTree.push(aVal);
        }
      }
      return dataTree;
    },

    /** 初始化部门树 */
    initOrgTree() {
      if (lscache.get("isSysRole")) {
        service.orgTreeDataQuery(data => {
          this.orgAllList = data.data;
          this.dataTree = this.orgTreeData(
            data.data,
            "id",
            "parentId",
            "children"
          );
          this.handleNodeClick(this.dataTree[0]);
        });
      } else if (lscache.get("isDeptRole")) {
        service.orgSelfTreeDataQuery(data => {
          this.orgAllList = data.data;
          this.dataTree = this.orgTreeData(
            data.data,
            "id",
            "parentId",
            "children"
          );
          this.handleNodeClick(this.dataTree[0]);
        });
      }
    },

    filterNode(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },

    /** 刷新树节点 */
    handleRefresh(isRefresh) {
      /** 初始化左侧树数据 */
      if (lscache.get("isSysRole")) {
        service.orgTreeDataQuery(data => {
          this.orgAllList = data.data;
          this.dataTree = this.orgTreeData(
            data.data,
            "id",
            "parentId",
            "children"
          );
          isRefresh && this.handleNodeClick(this.dataTree[0]);
          this.filterText = "";
        });
      } else if (lscache.get("isDeptRole")) {
        service.orgSelfTreeDataQuery(data => {
          this.orgAllList = data.data;
          this.dataTree = this.orgTreeData(
            data.data,
            "id",
            "parentId",
            "children"
          );
          isRefresh && this.handleNodeClick(this.dataTree[0]);
          this.filterText = "";
        });
      }
    },
    /** 树节点点击事件 */
    handleNodeClick(value, target) {
      let that = this;
      setTimeout(function() {
        $(".el-tree-node__label").each(function(key, html) {
          if (html.textContent == value.name) {
            html.parentElement.parentElement.classList.add("is-current");
            if (target && target.userName!=undefined) {
              that.orgLzscroll.$ele[0].scrollTop = 36 * key;
            };
          } else {
            html.parentElement.parentElement.classList.remove("is-current");
          };
        });
      }, 200);

      /** 请求当前部门详细信息 */
      service.orgInfoQuery(value.id, data => {
        let orgInfo = data.data;
        orgInfo.orderBy = String(orgInfo.orderBy);
        if (orgInfo.parentId == 0) {
          this.orgInfo = orgInfo;
          this.orgInfo.parentName = "根部门";
        } else {
          service.orgInfoQuery(orgInfo.parentId, data => {
            this.orgInfo = orgInfo;
            this.orgInfo.parentName = data.data.name;
          });
        }
      });
      /** 请求部门下下属部门的列表 */
      service.orgChildOrgListQuery(value.id, data => {
        this.tableAllData = data.data;
      });
      /** 请求部门下所有用户的列表 */
      service.orgChildUserListQuery(value.id, data => {
        this.userTableData = data.data;
        if (target && target.userName!=undefined) {
          _.filter(data.data, function(item, key) {
            if (item.userName == target.userName) {
              setTimeout(() => {
                that.userLzscroll.$ele[0].scrollTop = 40 * key;
                $(".el-tooltip").each(function(key, html) {
                  if (html.textContent == target.userName) {
                    html.parentElement.parentElement.classList.add(
                      "current-row"
                    );
                  };
                });
              }, 200);
            };
          });
        };
      });
      /** 请求部门下所有角色的列表 */
      service.orgChildRoleListQuery(value.id, data => {
        this.roleTableData = data.data;
      });
    },

    /** 新增组织机构 */
    handleOrgAdd() {
      this.$refs["vOrgDialog"].dialogVisible = true;
      this.$refs["vOrgDialog"].orgInfo = _.cloneDeep(this.orgInfo);
      this.$refs["vOrgDialog"].orgAllList = _.cloneDeep(this.orgAllList);
    },
    /** 删除当前组织机构 */
    handleOrgDelete() {
      if (this.orgInfo.parentId == 0) {
        this.$message({
          type: "warning",
          message: "根部门不可删除!"
        });
      } else {
        this.$confirm("将永久删除该组织信息, 是否继续?", "提示", {
          confirmButtonText: "确定",
          confirmButtonClass: "btn-ok",
          cancelButtonText: "取消",
          cancelButtonClass: "btn-back-out",
          type: "warning"
        })
          .then(() => {
            service.deleteChildOrg(this.orgInfo.id, data => {
              if (data.code != 200) {
                this.$message({
                  type: "info",
                  message: "请先删除本部门下的用户或角色后,再重新尝试删除本部门!"
                });
              } else {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.handleRefresh(true);
              }
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    /** 查看下属机构 */
    handleChildOrgObserve(index, row) {
      this.$refs["vChildOrgDialog"].dialogVisible = true;
      service.orgInfoQuery(row.parentId, data => {
        row.parentName = data.data.name;
        this.$refs["vChildOrgDialog"].childOrgForm = row;
      });
    },
    /** 修改下属机构 */
    handleChildOrgEdit(index, row) {
      this.handleNodeClick(row);
    },
    /** 删除下属机构 */
    handleChildOrgDelete(index, row) {
      this.$confirm("将永久删除此下属机构, 是否继续?", "提示", {
        confirmButtonText: "确定",
        confirmButtonClass: "btn-ok",
        cancelButtonText: "取消",
        cancelButtonClass: "btn-back-out",
        type: "warning"
      })
        .then(() => {
          service.deleteChildOrg(row.id, data => {
            if (data.code != 200) {
              this.$message({
                type: "info",
                message: "请先删除本部门下的用户或角色后,再重新尝试删除本部门!"
              });
            } else {
              this.$message({
                type: "success",
                message: "删除部门成功!"
              });
              let allDataIndex = _.findIndex(this.tableAllData, function(item) {
                return row.id == item.id;
              });
              this.handleRefresh();
              this.tableAllData.splice(allDataIndex, 1);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    /** 新增部门下属人员 */
    handleUserAdd() {
      this.$refs["vUserDialog"].dialogVisible = true;
      this.$refs["vUserDialog"].orgInfo = _.cloneDeep(this.orgInfo);
    },
    /** 修改部门下属人员 */
    handleUserEdit(index, row) {
      this.$refs["vUpdateUserDialog"].dialogVisible = true;
      this.$refs["vUpdateUserDialog"].userId = row.id;
    },
    /** 删除部门下属人员 */
    handleUserDelete(index, row) {
      this.$confirm("将永久删除这个人员信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        confirmButtonClass: "btn-ok",
        cancelButtonText: "取消",
        cancelButtonClass: "btn-back-out",
        type: "warning"
      })
        .then(() => {
          service.getUserInfo(row.id, data => {
            if (data.code == 200) {
              if (
                data.data.deptList != undefined &&
                data.data.deptList.length == 1
              ) {
                service.deleteChildUser(row.id, data => {
                  this.$message({
                    type: "success",
                    message: "删除成功!"
                  });
                  let userTableIndex = _.findIndex(this.userTableData, function(
                    item
                  ) {
                    return row.id == item.id;
                  });
                  this.userTableData.splice(userTableIndex, 1);
                  service.deleteUserPhoto(row.photo);
                });
              } else {
                let param = {
                  deptId: this.orgInfo.id,
                  userId: row.id
                };
                service.removeUserFromOrg(param, data => {
                  this.$message({
                    type: "success",
                    message: "删除成功!"
                  });
                  let userTableIndex = _.findIndex(this.userTableData, function(
                    item
                  ) {
                    return row.id == item.id;
                  });
                  this.userTableData.splice(userTableIndex, 1);
                });
              }
            } else {
              this.$message({
                type: "info",
                message: "删除失败"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    /** 部门下属人员添加角色 */
    handleRoleToUser(index, row) {
      this.$refs["vRoleToUserDialog"].dialogVisible = true;
      this.$refs["vRoleToUserDialog"].userInfo = _.cloneDeep(row);
      this.$refs["vRoleToUserDialog"].orgInfo = _.cloneDeep(this.orgInfo);
    },
    /** 部门下属人员授权管理员 */
    handleIsAdmin(index, row) {
      let isAdminParam = {
        deptId: this.orgInfo.id,
        userId: row.id
      };
      /** 1是管理员  0不是管理员 */
      if (row.isDeptAdmin == 1) {
        isAdminParam.isAdmin = false;
      } else {
        isAdminParam.isAdmin = true;
      }
      this.$confirm("修改管理员信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        confirmButtonClass: "btn-ok",
        cancelButtonText: "取消",
        cancelButtonClass: "btn-back-out",
        type: "warning"
      })
        .then(() => {
          service.updateUserIsAdmin(isAdminParam, data => {
            this.$message({
              type: "success",
              message: "修改管理员成功!"
            });
            this.handleNodeClick(this.orgInfo);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消修改管理员"
          });
        });
    },
    /** 新增部门下属角色 */
    handleRoleAdd() {
      this.$refs["vRoleDialog"].dialogVisible = true;
      this.$refs["vRoleDialog"].orgInfo = _.cloneDeep(this.orgInfo);
    },
    /** 修改部门下属角色 */
    handleRoleEdit(index, row) {
      this.$refs["vUpdateRoleDialog"].dialogVisible = true;
      this.$refs["vUpdateRoleDialog"].updateRoleForm = _.cloneDeep(row);
      this.$refs["vUpdateRoleDialog"].orgInfo = _.cloneDeep(this.orgInfo);
    },
    /** 删除部门下属角色 */
    handleRoleDelete(index, row) {
      this.$confirm("将永久删除这个下属角色信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        confirmButtonClass: "btn-ok",
        cancelButtonText: "取消",
        cancelButtonClass: "btn-back-out",
        type: "warning"
      })
        .then(() => {
          service.deleteChildRole(row.id, data => {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            let roleTableIndex = _.findIndex(this.roleTableData, function(
              item
            ) {
              return row.id == item.id;
            });
            this.roleTableData.splice(roleTableIndex, 1);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    /** 部门下属角色授权及添加用户 */
    handleUserToRole(index, row) {
      this.$refs["vUserToRoleDialog"].dialogVisible = true;
      this.$refs["vUserToRoleDialog"].roleInfo = _.cloneDeep(row);
      this.$refs["vUserToRoleDialog"].orgInfo = _.cloneDeep(this.orgInfo);
    },
    /** 重置表单 */
    resetForm() {
      /** 获取机构信息 */
      service.orgInfoQuery(this.orgInfo.id, data => {
        let orgInfo = data.data;
        orgInfo.orderBy = String(orgInfo.orderBy);
        if (orgInfo.parentId == 0) {
          this.orgInfo = orgInfo;
          this.orgInfo.parentName = "根部门";
        } else {
          service.orgInfoQuery(orgInfo.parentId, data => {
            this.orgInfo = orgInfo;
            this.orgInfo.parentName = data.data.name;
          });
        }
      });
    },
    /** 保存提交 */
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.orgInfo);
          delete param.parentName;
          delete param.perRoles;
          delete param.perUsers;
          /** 是否存在相同的部门名称 */
          let isRepeatOrgName = _.find(this.orgAllList, function(item) {
            return param.name == item.name;
          });
          if (isRepeatOrgName && isRepeatOrgName.id != param.id) {
            this.$message({
              type: "info",
              message: "已有此名称的部门,修改失败！"
            });
            this.resetForm();
          } else {
            service.updateOrgInfo(param, data => {
              if (data.code == 200) {
                this.$message({
                  type: "success",
                  message: "部门修改成功！"
                });
                this.handleRefresh();
              }
            });
          }
        } else {
          return false;
        }
      });
    },

    /** 分页修改每页数量后执行的函数 */
    handleSizeChange(val) {
      this.$data.tablePageData = [];
      this.$data.pageSize = val;
      this.$data.tablePageData = _.chunk(
        _.cloneDeep(this.$data.tableAllData),
        val
      )[0];
    },

    /** 页码改变后执行的函数 */
    handleCurrentChange(val) {
      this.$data.tablePageData = [];
      this.$data.tablePageData = _.chunk(
        _.cloneDeep(this.$data.tableAllData),
        this.$data.pageSize
      )[val - 1];
    },

    /** 按联系电话和用户名搜索用户 */
    handleSearchUser() {
      this.$prompt(" ", "请输入用户名或联系电话", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        confirmButtonClass:'btn-ok',
        cancelButtonClass: 'btn-back-out',
        inputValidator: value => {
          return value === null ? false : true;
        },
        inputErrorMessage: "请输入......"
      })
        .then(({ value }) => {
          commonOper.showLoading("正在查询，请稍候...");
          service.getSearchUser(value, data => {
            if (data.data.length != 0) {
              this.$refs["vSearchDialog"].dialogVisible = true;
              this.$refs["vSearchDialog"].userList = _.cloneDeep(data.data);
            } else {
              /** 关闭等待页面 */
              commonOper.closeLoading();
              this.$message({
                type: "info",
                message: "暂无数据！"
              });
            }
          });
        })
        .catch(() => {
          console.log("取消输入!");
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass">
@import './user_manage.scss'
</style>



