<template>
  <div class="sidebar uk-flex uk-flex-item-none" :class="[isCollapse ? 'sidebar-collapse' : '']">
    <el-menu class="frist-menu" ref="menu" background-color="#324157" text-color="#fff" :default-active="onRoutes" @select="handleSelect"
      :class="classObject" :collapse="isCollapse" unique-opened router>
        <div class="sidemenu-top-title">
            <span class="sidemenu-top-text" v-if="!isCollapse">导航菜单</span>
            <span class="sidemenu-tools" :class="[isCollapse?'sidemenu-tools-right':'sidemenu-tools-left']">
                <img :src="shrinkImg" :title="toggleTitle" @click="handleSidemenuToggle">
            </span>
        </div>
        <template v-for="(menu, index) in menus">
            <el-menu-item :index="menu.path" v-if="!menu.children || menu.children.length==0" :key="index">
                <img class="sidemenu-icon" :src="menu.img">
                <span slot="title" class="sidemenu-title">{{ menu.name }}</span>
            </el-menu-item>
            <el-submenu :index="getIndex(index)" v-else :key="index">
                <template slot="title">
                    <img class="sidemenu-icon" :src="menu.img">
                    <span slot="title" class="sidemenu-title">{{ menu.name }}</span>
                </template>
                <el-menu-item :index="submenu.path" v-for="(submenu,subindex) in menu.children" :key="index+'-'+subindex">
                    <span class="sidemenu-title sub-sidemenu-title">{{ submenu.name }}</span>
                </el-menu-item>
            </el-submenu>
        </template>
    </el-menu>
  </div>
</template>

<script>
import menus from "../../assets/scripts/config/menu_config.js";
import loginOper from "../../assets/scripts/common/loginOper.js";

export default {
  data() {
    return {
      state: "init",
      // 菜单栏是否收缩
      isCollapse: false,
      // 是否显示菜单栏头部
      isShowTopTitle: true,
      // 后台服务获取当前用户权限
      permissions: [],
      // 当前用户菜单
      menus: null,
      // 默认菜单
      defaultMenus: _.cloneDeep(menus),
      // 当前系统登录用户编号
      userId: "",
      index: 0,
      /** 判断是否默认加载第一个菜单，手动单击一级菜单时执行 */
      isTriggerMenu: false,
      /** 收缩按钮图标 */
      shrinkImg: require("../../assets/images/sidebar/shrink.png"),
      spreadImg: require("../../assets/images/sidebar/spread.png")
    };
  },
  computed: {
    onRoutes() {
      return this.$route.path.replace("/", "");
    },
    isLeaf(menu) {
      return menu.children && menu.children.length > 0;
    },
    classObject() {
      return {
        "sidebar-menu": !this.isCollapse
      };
    },
    toggleTitle() {
      return this.isShowTopTitle ? "菜单收缩" : "菜单展开";
    }
  },
  created() {
    let that = this;

    // 如果当前缓存用户为空，请求服务端获取当前登录用户
    // if (lscache.get("userId") == null) {
    //   this.getUserInfo();
    // } else if (lscache.get("permissions") == null) {
    //   this.getPermission();
    // } else {
    //   this.calMenus();
    // }

    if (lscache.get("user") == 'cntzc') {
      this.menus = menus[0];
    } else if (lscache.get("user") == 'admin') {
      this.menus = menus[1];
    } else {
      this.$router.push("/Login");
    }

    let path = window.location.href.split("#")[1];
    if (path == "/Main") {
        lscache.get("user") == 'cntzc' ? this.$router.push("/HomePage") : this.$router.push("/ManageHome")
    }
  },
  mounted() {
    let that = this;

    // 根据屏幕分辨率设置菜单收缩
    this.isCollapse = document.body.scrollWidth >= 1600 ? false : true;

    // 响应浏览器窗体宽度修改事件
    this.$bus.$on("triScreenResize", value => {
      this.isCollapse = value.width >= 1600 ? false : true;
    });
  },
  methods: {
    getIndex(index) {
      return index + "";
    },
    handleSelect() {
      let that = this;
    //   let permissions = lscache.get("permissions");
    },
    // 菜单收缩与展开切换
    handleSidemenuToggle() {
      let that = this;
      if (!this.isCollapse) {
        this.isShowTopTitle = false;
        this.$nextTick(() => {
          this.isCollapse = !this.isCollapse;
        });
      } else {
        this.isCollapse = !this.isCollapse;
        this.$nextTick(() => {
          this.isShowTopTitle = true;
        });
      }
    },
    // 获取当前用户信息
    getUserInfo() {
      let that = this;
      loginOper.getUserInfo({
        successFunc: data => {
          that.userId = lscache.get("userId");
          that.$bus.$emit("triSetUserInfo");
          /** 往首页发送当前系统的登录用户信息 */
          that.$bus.$emit("SetHomePageUserInfo");
        }
      });
    },
    // 获取当前用户权限
    getPermission() {
      let that = this;
      loginOper.getPermission({
        userId: lscache.get("userId"),
        successFunc: data => {
          that.permissions = data;
          // 计算当前系统的加载菜单
          this.calMenus();
        }
      });
    },
    // 计算当前页面菜单
    calMenus() {
      let pmsOne = lscache.get("pmsOne");
      let pmsTwo = lscache.get("pmsTwo");

      // 根据后台请求权限，设置页面菜单
      // 设置一级菜单
      this.menus = _.filter(this.defaultMenus, item => {
        return _.find(pmsOne, {
          perValue: item.path
        });
      });
      // 设置二级菜单
      _.map(this.menus, item => {
        if (item.children && item.children.length > 0) {
          item.children = _.filter(item.children, subItem => {
            return _.find(pmsTwo, {
              perValue: subItem.path
            });
          });
        }
      });
    }
  },
  watch: {
    userId(nValue, oValue) {
      if (nValue) {
        this.getPermission();
      }
    },
    menus(nValue, oValue) {
      // 系统初始化时，默认加载第一个菜单或其子菜单
      if (
        nValue &&
        nValue.length > 0 &&
        this.$router.currentRoute.path == "/Main"
      ) {
        if (!nValue[0].children && nValue[0].path) {
          this.$router.push("/" + nValue[0].path);
          this.$refs["menu"].activeIndex = nValue[0].path;
        } else if (nValue[0].children && nValue[0].children[0].path) {
          this.$router.push("/" + nValue[0].children[0].path);
          this.$refs["menu"].activeIndex = nValue[0].children[0].path;
        }
      }
    },
    isCollapse(nValue, oValue) {
      this.$nextTick(() => {
        // 改变菜单收缩状态后，触发事件
        this.$bus.$emit("triSidebarStateChange");
      });
    }
  }
};
</script>

<style>
.sidebar {
  height: 100%;

  -webkit-flex-shrink: 1;
  -ms-flex-negative: 1;
  flex-shrink: 1;

  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 2px 4px 8px rgba(0, 0, 0, 0.28);
}
.sidebar-menu li {
  width: 240px;
}

.sidebar .el-menu-item.is-active {
  color: #ffffff;
}

.el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item:hover,
.el-menu--horizontal.el-menu--dark .el-submenu .el-submenu-title:hover,
.el-menu-item:hover {
  background-color: #124681;
}
.el-menu--collapse .el-submenu .el-menu {
  top: inherit;
  bottom: 0;
}
/* 防止滚动轴的样式文件导致Dom元素样式修改 */

.sidebar-menu {
  padding: 0 !important;
  font-family: '微软雅黑';
}

.sidemenu-title {
  margin-left: 15px;
}

.sub-sidemenu-title {
  margin-left: 33px;
}
/** 左侧菜单收缩与展开样式 */

.sidemenu-tools {
  float: right;
  cursor: pointer;
}

.sidemenu-tools-left img {
  will-change: transform;
  -webkit-transition: all 2s cubic-bezier(0.4, 0, 0.2, 1);
  transition: all 2s cubic-bezier(0.4, 0, 0.2, 1);

  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

.sidemenu-tools-right img {
  will-change: transform;
  -webkit-transition: all 2s cubic-bezier(0.4, 0, 0.2, 1);
  transition: all 2s cubic-bezier(0.4, 0, 0.2, 1);

  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
}

.sidemenu-top-title {
  height: 16px;
  color: white;
  padding: 20px;
  font-size: 18px;
}

.sidebar-collapse .sidemenu-top-text {
  display: none;
}
</style>


