import _ from "lodash";

// 导入通用的组件
import Main from '@/components/common/Main.vue'
import Login from '@/components/common/Login.vue'

// 首页
import HomePage from "@/pages/home_page/HomePage.vue";

// 基础信息
import BaseInfo from "@/pages/info_manage/BaseInfo.vue";
// 人员管理
import UserManage from "@/pages/info_manage/user_manage/UserManage.vue";
// 设备管理
import DeviceManage from "@/pages/info_manage/DeviceManage.vue";
// 资质管理
import AptitudeManage from "@/pages/info_manage/AptitudeManage.vue";
// 客户管理
import ClientManage from "@/pages/info_manage/ClientManage.vue";
// 污水点管理
import SewageManage from "@/pages/info_manage/SewageManage.vue";
// 视频点管理
import VideoManage from "@/pages/info_manage/VideoManage.vue";

// 出入场管理
import OutInManage from "@/pages/outin_manage/OutInManage.vue";

// 视频监测
import VideoMonitor from "@/pages/real_time_monitor/VideoMonitor.vue";
// 排放点监测
import SewageMonitor from "@/pages/real_time_monitor/SewageMonitor.vue";
// 车辆监测
import CarMonitor from "@/pages/real_time_monitor/CarMonitor.vue";

// 添加检疫
import AddQuarantine from "@/pages/quarantine_manage/AddQuarantine.vue";
// 检疫记录
import QuarantineRecord from "@/pages/quarantine_manage/QuarantineRecord.vue";

/** 管理端 */
// 管理端首页
import ManageHome from "@/pages/manage/ManageHome.vue";
// 屠宰场管理
import SlaughteManage from "@/pages/manage/SlaughteManage.vue";
// 污染点检查
import PollutionDetection from "@/pages/manage/PollutionDetection.vue";
// 屠宰场统计
import SlaughteCensus from "@/pages/manage/SlaughteCensus.vue";
// 检疫统计
import QuarantineCensus from "@/pages/manage/QuarantineCensus.vue";

// 首页
const commonRoutes = [{
    path: '/HomePage',
    component: HomePage
}];

// 信息管理路由
const infoRoutes = [{
    path: '/BaseInfo',
    component: BaseInfo
}, {
    path: '/UserManage',
    component: UserManage
}, {
    path: '/DeviceManage',
    component: DeviceManage
}, {
    path: '/AptitudeManage',
    component: AptitudeManage
}, {
    path: '/ClientManage',
    component: ClientManage
}, {
    path: '/SewageManage',
    component: SewageManage
}, {
    path: '/VideoManage',
    component: VideoManage
}];

// 出入场管理
const outInRoutes = [{
    path: '/OutInManage',
    component: OutInManage
}];

// 实时监测
const monitorRoutes = [{
    path: '/VideoMonitor',
    component: VideoMonitor
}, {
    path: '/SewageMonitor',
    component: SewageMonitor
}, {
    path: '/CarMonitor',
    component: CarMonitor
}];

// 检疫管理
const quarantineRoutes = [{
    path: '/AddQuarantine',
    component: AddQuarantine
}, {
    path: '/QuarantineRecord',
    component: QuarantineRecord
}]

// 管理端路由
const manageRoutes = [{
    path: '/ManageHome',
    component: ManageHome
}, {
    path: '/SlaughteManage',
    component: SlaughteManage
}, {
    path: '/PollutionDetection',
    component: PollutionDetection
}, {
    path: '/SlaughteCensus',
    component: SlaughteCensus
}, {
    path: '/QuarantineCensus',
    component: QuarantineCensus
}]

// 合并路由
let routes = _.union(commonRoutes, []);
routes = _.union(routes, infoRoutes);
routes = _.union(routes, outInRoutes);
routes = _.union(routes, monitorRoutes);
routes = _.union(routes, quarantineRoutes);
routes = _.union(routes, manageRoutes);

export default [{
    path: '/',
    redirect: '/Main'
}, {
    path: '/Main',
    component: Main,
    children: routes
}, {
    path: '/Login',
    component: Login
}]


