import ajaxOper from '../../../../assets/scripts/ajax/ajaxOper.js'

export default {
    /** 请求组织机构树数据 */
    orgTreeDataQuery(successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/dept/listAll',
            successFunc
        })
    },

    /** 部门管理员请求本部门数据 */
    orgSelfTreeDataQuery(successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/dept/listSelf',
            successFunc
        })
    },

    /** 请求当前部门详细信息 */
    orgInfoQuery(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/dept/findOne?deptId=' + id,
            successFunc
        })
    },

    /** 请求部门下下属部门的列表 */
    orgChildOrgListQuery(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/dept/listFChildDept?deptId=' + id,
            successFunc
        })
    },

    /** 请求部门下所有用户的列表 */
    orgChildUserListQuery(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/user/listByDeptId?deptId=' + id,
            successFunc
        })
    },

    /** 请求部门下所有角色的列表 */
    orgChildRoleListQuery(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/role/listByDeptId?deptId=' + id,
            successFunc
        })
    },

    /** 请求当前用户所包含的角色列表 */
    userChildRolesQuery(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            url: '/permission/user/findUserRoles?userId=' + id,
            successFunc
        })
    },

    /** 修改下属部门 */
    updateOrgInfo(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            data: param,
            url: '/permission/dept/update',
            successFunc
        })
    },

    /** 删除下属部门 */
    deleteChildOrg(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            url: '/permission/dept/delete?deptId=' + id,
            successFunc
        })
    },

    /** 新增下属部门 */
    addChildOrg(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            data: param,
            url: '/permission/dept/save',
            successFunc
        })
    },

    /** 修改用户管理员信息 */
    updateUserIsAdmin(param, successFunc){
        ajaxOper.ajaxHttp({
            type: 'post',
            url: '/permission/user/updateDeptAdmin?deptId=' + param.deptId + '&userId=' + param.userId + '&isAdmin=' + param.isAdmin,
            successFunc
        })
    },

    /** 修改部门下用户所包含的角色 */
    updatUserRole(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            postType: 'form',
            data: param,
            url: '/permission/user/updateUserRoles',
            successFunc
        })
    },

    /** 修改部门下属用户 */
    updateChildUser(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            data: param,
            url: '/permission/user/update',
            successFunc
        })
    },

    /** 删除部门下属用户 */
    deleteChildUser(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            url: '/permission/user/delete?userId=' + id,
            successFunc
        })
    },

    /** 新增部门下属用户 */
    addChildUser(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            data: param.user,
            url: '/permission/user/save?deptId=' + param.ids.deptId + '&roleIds=' + param.ids.roleIds + '&isAdmin=' + param.ids.isAdmin,
            successFunc
        })
    },

    /** 修改部门下属角色 */
    updateChildRole(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            data: param,
            url: '/permission/role/update',
            successFunc
        })
    },

    /** 删除部门下属角色 */
    deleteChildRole(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            url: '/permission/role/delete?roleId=' + id,
            successFunc
        })
    },

    /** 新增部门下属角色 或者 新增公共角色*/
    addChildRole(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            data: param.role,
            url: 'permission/role/save?' + param.url,
            successFunc
        })
    },

    /** 初始化权限树 */
    powerTreeQuery(type, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/permission/listByType?type=' + type,
            successFunc
        })
    },

    /** 修改角色下用户列表 */
    updatRoleUser(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            postType: 'form',
            data: param,
            url: '/permission/user/updatRoleUser',
            successFunc
        })
    },

    /** 修改角色下权限列表 */
    updateRolePower(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            postType: 'form',
            data: param,
            url: '/permission/permission/updateRolePermission',
            successFunc
        })
    },

    /** 获取当前角色下用户 */
    roleChildUserListQuery(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/user/listByRoleId?roleId=' + id,
            successFunc
        })
    },

    /** 获取当前角色下权限 */
    roleChildPowerListQuery(id, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/permission/listByRoleId?roleId=' + id,
            successFunc
        })
    },

    /** 重置密码 */
    resetPassWordQuery(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            url: '/permission/user/resetPwd?userId=' + param.userId + '&pwd=' + param.pwd,
            successFunc
        })
    },

    /** 删除图片 */
    deleteUserPhoto(id) {
        ajaxOper.ajaxHttp({
            type: 'post',
            url: baseURL + '/fastdfs/file/delete?ids=' + id,
        })
    },

    /** 上传base64编码图片 */
    uploadBase64(imgUrl, successFunc){
        ajaxOper.ajaxHttp({
            type: 'post',
            postType: 'base64',
            data:imgUrl,
            url: baseURL + '/fastdfs/file/upload/nomark/base64',
            successFunc
        })
    },

    /** 根据用户名称或者联系电话获取用户信息 */
    getSearchUser(nameOrPhone, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/user/findByNameOrPhone?nameOrPhone=' + nameOrPhone,
            successFunc
        })
    },

    /** 根据用户ID查询用户信息 */
    getUserInfo(userId, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/user/findOneAll?userId=' + userId,
            successFunc
        })
    },

    /** 修改所属部门与用户之间的联系 */
    updateUserOrg(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'post',
            url: '/permission/user/updateUserDept?oldDeptId='+ param.oldDeptId +'&newDeptId='+ param.newDeptId +'&userId=' + param.userId,
            successFunc
        })
    },

    /** 获取公共角色 */
    getPublicRole(isPublic, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/role/listByIsPublic?isPublic=' + isPublic,
            successFunc
        })
    },

    /** 将用户从部门移除 */
    removeUserFromOrg(param, successFunc) {
        ajaxOper.ajaxHttp({
            type: 'get',
            url: '/permission/user/removeUserFromDept?deptId=' + param.deptId + '&userId=' + param.userId,
            successFunc
        })
    }
}


