<template>
  <div class="base-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="base-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span>编辑基础信息</span>
            </div>
            <div class="content">
                <happy-scroll class="base-scroll" color="rgba(73, 163, 225,0.2)" hide-horizontal resize size="4">
                    <el-form :model="baseInfoForm" :rules="baseInfoRules" ref="baseInfoForm" label-width="140px">
                        <el-form-item label="企业名称" prop="qymc">
                            <el-input v-model="baseInfoForm.qymc" clearable placeholder="请输入企业名称"></el-input>
                        </el-form-item>
                        <el-form-item label="所在地区" prop="szdq">
                            <el-cascader v-model="baseInfoForm.szdq" clearable placeholder="请选择所在地" :options="citysLv3" expand-trigger="hover" :props="{value: 'label'}"></el-cascader>
                        </el-form-item>
                        <el-row>
                          <el-col :span="12">
                            <el-form-item label="定点屠宰证号" prop="tzz">
                                <el-input v-model="baseInfoForm.tzz" clearable placeholder="请输入定点屠宰证号"></el-input>
                            </el-form-item>
                            <el-form-item label="屠宰场分类" prop="type">
                                <el-select v-model="baseInfoForm.type" clearable placeholder="请选择屠宰类型">
                                    <el-option label="屠宰场" :value="1"></el-option>
                                </el-select>
                            </el-form-item>
                          </el-col>
                          <el-col :span="12">
                            <el-form-item label="设计屠宰量" prop="tzl">
                                <el-input v-model="baseInfoForm.tzl" clearable placeholder="请输入屠宰量"></el-input>
                            </el-form-item>
                            <el-form-item label="邮编" prop="code">
                                <el-input v-model="baseInfoForm.code" clearable placeholder="请输入邮编"></el-input>
                            </el-form-item>
                          </el-col>
                        </el-row>
                        <el-form-item label="地址描述" prop="address">
                            <el-input v-model="baseInfoForm.address" clearable placeholder="请输入地址"></el-input>
                        </el-form-item>
                        <el-form-item label="自营" prop="ziying">
                            <el-checkbox-group v-model="baseInfoForm.ziying">
                                <el-checkbox label="生猪"></el-checkbox>
                                <el-checkbox label="活牛"></el-checkbox>
                                <el-checkbox label="活羊"></el-checkbox>
                                <el-checkbox label="活鸡"></el-checkbox>
                                <el-checkbox label="活鸭"></el-checkbox>
                                <el-checkbox label="活鹅"></el-checkbox>
                                <el-checkbox label="其他畜类"></el-checkbox>
                                <el-checkbox label="其他家禽"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="待宰" prop="daizai">
                            <el-checkbox-group v-model="baseInfoForm.daizai">
                                <el-checkbox label="生猪"></el-checkbox>
                                <el-checkbox label="活牛"></el-checkbox>
                                <el-checkbox label="活羊"></el-checkbox>
                                <el-checkbox label="活鸡"></el-checkbox>
                                <el-checkbox label="活鸭"></el-checkbox>
                                <el-checkbox label="活鹅"></el-checkbox>
                                <el-checkbox label="其他畜类"></el-checkbox>
                                <el-checkbox label="其他家禽"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="法定代表人" prop="daibiao">
                                    <el-input v-model="baseInfoForm.daibiao" clearable placeholder="请输入法定代表人"></el-input>
                                </el-form-item>
                                <el-form-item label="联系人" prop="lianxiren">
                                    <el-input v-model="baseInfoForm.lianxiren" clearable placeholder="请输入联系人"></el-input>
                                </el-form-item>
                                <el-form-item label="注册资金(万元)" prop="zijin">
                                    <el-input v-model="baseInfoForm.zijin" clearable placeholder="请输入注册资金"></el-input>
                                </el-form-item>
                                <el-form-item label="是否牌证齐全" prop="paizheng">
                                    <el-radio v-model="baseInfoForm.paizheng" label="1">是</el-radio>
                                    <el-radio v-model="baseInfoForm.paizheng" label="0">否</el-radio>
                                </el-form-item>
                                <el-form-item label="是否月报企业" prop="yuebao">
                                    <el-radio v-model="baseInfoForm.yuebao" label="1">是</el-radio>
                                    <el-radio v-model="baseInfoForm.yuebao" label="0">否</el-radio>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="法人代码" prop="farendaima">
                                    <el-input v-model="baseInfoForm.farendaima" clearable placeholder="请输入法人代码"></el-input>
                                </el-form-item>
                                <el-form-item label="联系电话" prop="phone">
                                    <el-input v-model="baseInfoForm.phone" clearable placeholder="请输入电话号码"></el-input>
                                </el-form-item>
                                <el-form-item label="注册登记类型" prop="dengjitype">
                                    <el-select v-model="baseInfoForm.dengjitype" clearable placeholder="请选择登记类型">
                                        <el-option label="私营独资企业" :value="1"></el-option>
                                        <el-option label="国营企业" :value="2"></el-option>
                                        <el-option label="合资企业" :value="3"></el-option>
                                        <el-option label="其他" :value="4"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="是否周报企业" prop="zhoubao">
                                    <el-radio v-model="baseInfoForm.zhoubao" label="1">是</el-radio>
                                    <el-radio v-model="baseInfoForm.zhoubao" label="0">否</el-radio>
                                </el-form-item>
                                <el-form-item label="是否规模企业" prop="guimo">
                                    <el-radio v-model="baseInfoForm.guimo" label="1">是</el-radio>
                                    <el-radio v-model="baseInfoForm.guimo" label="0">否</el-radio>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="备注" prop="remark">
                            <el-input v-model="baseInfoForm.remark" type="textarea" :rows="4" placeholder="请输入备注"></el-input>
                        </el-form-item>
                    </el-form>
                </happy-scroll>
                <div slot="footer" class="bottom-footer">
                    <el-button size="small" class="btn-common btn-back" @click="handleReset()">重置</el-button>
                    <el-button size="small" class="btn-common btn-ok" @click="handleSave()">保存</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";
/** 城市数据 */
import { regionData } from "element-china-area-data";

export default {
  data () {
      return {
          citysLv3: regionData, // 三级城市数据
          baseInfoForm: {
              qymc: '城南屠宰场',
              szdq: ['陕西省','西安市'],
              tzz: 'TZ25124521',
              type: 1,
              tzl: '50000',
              code: '710100',
              address: '陕西省西安市长安区子午大道99号',
              ziying: ["活鸭"],
              daizai: ["生猪"],
              daibiao: '张三',
              lianxiren: '李四',
              zijin: '500',
              paizheng: "1",
              yuebao: "1",
              farendaima: 'XA52152425',
              phone: '13521524259',
              dengjitype: 1,
              zhoubao: "1",
              guimo: "1",
              remark: '这是城南屠宰场'
          },
          baseInfoRules: {

          }
      }
  },

  mounted () {
      
  },

  created () {
      
  },

  watch: {
      
  },

  methods: {
      /** 重置 */
      handleReset() {
          this.baseInfoForm = {
              type: '',
              ziying: ["活鸭"],
              daizai: ["生猪"],
              paizheng: "1",
              yuebao: "1",
              zhoubao: "1",
              guimo: "1",
          }
      },
      /** 保存 */
      handleSave() {

      }
  },

}
</script>

<style lang="sass">
@import './base_info.scss'
</style>



