<template>
    <div class="header">
        <div class="header-content">
            <div class="header-logo">
                <img :src="imgLogo" alt="组织权限管理平台">
            </div>
            <div class="header-user">
              <img class="header-user-img" :src="imgUser">
              <span class="header-user-name">欢迎您，{{userName}}</span>
              <img class="header-split" :src="imgSplit" alt="分割线">
              <img class="header-logout" @click="handleLogout" :src="imgLogOut" alt="注销">
            </div>
        </div>
    </div>
</template>

<script>
import loginOper from "../../assets/scripts/common/loginOper.js";
export default {
  data() {
    return {
      imgLogo: require("../../assets/images/main/logo.png"),
      imgUser: require("../../assets/images/main/defavatar.png"),
      imgLogOut: require("../../assets/images/main/icon_quit.png"),
      imgSplit: require("../../assets/images/main/split.png"),
      userName: "管理员"
    };
  },
  mounted() {
    let that = this;
    // 设置头部用户信息
    if (lscache.get("userName") != null) {
      that.userName = lscache.get("userName");
    //   that.imgUser =
    //     baseURL + "/permission/user/photo/" + lscache.get("userId");
    } else {
      this.$bus.$on("triSetUserInfo", value => {
        that.userName = lscache.get("userName");
        // that.imgUser =
        //   baseURL + "/permission/user/photo/" + lscache.get("userId");
      });
    }

    if (lscache.get("userId") == null) {
      that.imgUser = require("../../assets/images/main/defavatar.png");
    }

    // 页面关闭时清除本地缓存
    // window.onbeforeunload = function() {
    //     debugger
    //   // 清楚本地缓存设置
    //   lscache.flush();
    // };
  },

  methods: {
    handleLogout() {
      this.$confirm("注销当前登录用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        confirmButtonClass: "btn-ok",
        cancelButtonText: "取消",
        cancelButtonClass: "btn-back-out",
        type: "warning"
      })
        .then(() => {
        // 注销前先完全销毁组件
        //   this.$root.$destroy();
        //   loginOper.userLogout();
        // 清除本地缓存设置
        lscache.flush();
        this.$router.push("/Login");
        })
        .catch(() => {
          console.log("取消注销账户");
        });
    }
  }
};
</script>

<style>
.header {
  padding: 0 20px;
  height: 80px;
  will-change: margin;
  -webkit-transition: margin 280ms;
  -moz-transition: margin 280ms;
  transition: margin 280ms;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background-color: #ffffff;
  display: -webkit-flex;
  align-items: center;
  background-image: url('../../assets/images/main/top.png')
}

.header-content {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 100%;
}

.header-logo {
  float: left;
}

.header-user {
  float: right;
  line-height: 64px;
  color: #ffffff;
}

.header-user-img {
  height: 45px;
  width: 45px;
  border-radius: 100px;
  margin-right: 10px;
}

.header-split {
  margin-left: 10px;
  margin-right: 10px;
}

.header-logout {
  cursor: pointer;
}
</style>



