<template>
  <div class="aptitude-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="aptitude-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span>资质管理</span>
            </div>
            <div class="content">
                <div :class="['apt', item.title == '企业资质' ? 'group-apt' : '']" v-for="(item, index) in apt" :key="index">
                    <div class="aptitude-title">
                        <span>{{ item.title }}：</span>
                        <span style="float: right;margin-top: -5px;">
                            <i class="md-icon material-icons" title="添加">&#xE145;</i>
                        </span>
                    </div>
                    <hr>
                    <div class="apt-content">
                        <el-card v-for="(itemData, indexItem) in item.data" :key="indexItem" shadow="hover">
                            <img :src="itemData.imgSrc" class="image">
                            <div class="apt-card-bottom">
                                <span>{{ itemData.name }}</span>
                                <i class="md-icon el-icon-download"></i>
                                <el-button class="btn-view" size="mini">查看</el-button>
                                <el-button type="danger" size="mini">删除</el-button>
                            </div>
                        </el-card>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
      apt: [
        {
          title: "企业资质",
          data: [
            {
              imgSrc: require("./images/yyzz.jpg"),
              name: "营业执照"
            },
            {
              imgSrc: require("./images/tzxkz.jpg"),
              name: "畜禽屠宰许可证"
            },
            {
              imgSrc: require("./images/spxkz.jpg"),
              name: "食品经营许可证"
            }
          ]
        },
        {
          title: "人员资质",
          data: [
            {
              imgSrc: require("./images/jkz.jpg"),
              name: "健康证"
            }
          ]
        }
      ]
    };
  },

  mounted() {},

  created() {},

  watch: {},

  methods: {
    /** 删除企业资质 */
    deleteOrgAptitude() {},

    /** 添加企业资质 */
    addOrgAptitude() {},

    /** 修改企业资质 */
    updateOrgAptitude() {},

    /** 删除检疫员资质 */
    deleteUserAptitude() {},

    /** 添加检疫员资质 */
    addUserAptitude() {},

    /** 修改检疫员资质 */
    updateUserAptitude() {}
  }
};
</script>

<style lang="sass">
@import './aptitude_manage.scss'
</style>



