<template>
    <div class="sewage-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="sewage-left uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span>添加污染点位</span>
            </div>
            <div class="sewage-left-content">
                <happy-scroll class="sewage-form" color="rgba(73, 163, 225,0.2)" hide-horizontal resize size="4">
                    <el-form :model="sewageForm" :rules="sewageRules" ref="sewageForm" label-width="100px">
                        <el-form-item label="位置名称" prop="sewageName">
                            <el-input v-model="sewageForm.sewageName" clearable placeholder="请输入位置名称"></el-input>
                        </el-form-item>
                        <el-form-item label="位置编号" prop="sewageNumber">
                            <el-input v-model="sewageForm.sewageNumber" clearable placeholder="请输入位置编号"></el-input>
                        </el-form-item>
                        <el-form-item label="污染类型" prop="type">
                            <el-select v-model="sewageForm.type" clearable placeholder="请选择污染类型">
                                <el-option label="大气污染" value="大气污染"></el-option>
                                <el-option label="水污染" value="水污染"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="污染程度描述" prop="levelInfo">
                            <el-input v-model="sewageForm.levelInfo" type="textarea" :rows="6" clearable placeholder="请输入污染程度描述"></el-input>
                        </el-form-item>
                        <el-form-item label="污染位置描述" prop="locationInfo">
                            <el-input v-model="sewageForm.locationInfo" type="textarea" :rows="6" clearable placeholder="请输入污染位置描述"></el-input>
                        </el-form-item>
                    </el-form>
                </happy-scroll>
                <div slot="footer" class="bottom-footer">
                    <el-button size="small" class="btn-common btn-back" @click="handleReset()">重置</el-button>
                    <el-button size="small" class="btn-common btn-ok" @click="handleSave()">保存</el-button>
                </div>
            </div>
        </div>
        <div class="uk-flex-item-none" style="width:1000px;">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>污染点列表</span>
            </div>
            <div class="sewage-right-content">
                <div class="demo-input-suffix">
                    <span>污染类型：</span>
                    <el-select v-model="type" placeholder="请选择污染类型" @change="sewageListQuery">
                        <el-option label="大气污染" value="大气污染"></el-option>
                        <el-option label="水污染" value="水污染"></el-option>
                    </el-select>
                </div>
                <el-table class="sewage-table" :data="sewageList" highlight-current-row>
                    <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                    <el-table-column prop="sewageName" label="位置名称" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="sewageNumber" label="位置编号" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="type" label="污染类型" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="levelInfo" label="污染程度描述" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="locationInfo" label="污染位置描述" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="操作" align="center" width="120">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
      /** 污染点列表 */
      sewageList: [
        {
          sewageName: "XXX河水污染",
          sewageNumber: "2011251",
          type: "水污染",
          levelInfo: "非常严重",
          locationInfo: "xxx路xxx街99号"
        }
      ],
      /** 添加污染点表单 */
      sewageForm: {},
      sewageRules: {},

      type: ""
    };
  },

  created() {},

  mounted() {},

  methods: {
    /** 重置表单 */
    handleReset() {},
    /** 保存污染点信息 */
    handleSave() {},

    /** 编辑表单修改污染点信息 */
    handleEdit(index, row) {},

    /** 查询污染点列表 */
    sewageListQuery() {},

    /** 删除污染点 */
    handleDelete(index, row) {}
  }
};
</script>

<style lang="sass">
@import './sewage_manage.scss'
</style>




