<template>
  <div class="quarantine-census-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="quarantine-census-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE01D;</i>
                </span>
                <span>检疫统计</span>
            </div>
            <div class="content">
                <el-form :model="slaughteForm" :rules="slaughteRules" ref="slaughteForm" label-width="120px">
                  <el-row>
                    <el-col :span="8">
                        <el-form-item label="屠宰场名称">
                            <el-input v-model="slaughteForm.name" clearable placeholder="请输入屠宰场名称"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="屠宰种类">
                            <el-select v-model="slaughteForm.type" clearable placeholder="请选择类型">
                                <el-option label="猪" value="猪"></el-option>
                                <el-option label="牛" value="牛"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="时间范围">
                            <el-date-picker v-model="slaughteForm.date" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" @change="handleCheckDate"></el-date-picker>
                        </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
                <div slot="footer" class="bottom-footer">
                    <el-button size="mini" class="btn-common btn-back" @click="handleReset()">重置</el-button>
                    <el-button size="mini" class="btn-common btn-query" @click="handleCensus()">统计</el-button>
                </div>
                <el-row type="flex" :gutter="10" class="mid-charts">
                    <el-col :span="8">
                        <el-card class="box-card">
                            <div class="gauge-echarts"></div>
                        </el-card>
                    </el-col>
                    <el-col :span="16">
                        <el-card class="box-card">
                            <div class="bar-echarts"></div>
                        </el-card>
                    </el-col>
                </el-row>
                <div class="bottom-table">
                    <el-card class="box-card">
                        <div class="title">异常信息</div>
                        <el-table class="abnormal-table" :data="abnormalInfoData" highlight-current-row height="calc(100% - 54px)">
                            <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                            <el-table-column prop="name" label="屠宰场名称" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="quarantiner" label="检疫员" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="type" label="异常类型" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="disease" label="相关疾病" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="level" label="严重程度" align="center" show-overflow-tooltip width="120px">
                                <template slot-scope="scope">
                                    <el-tag close-transition :class="scope.row.level == 0 ? 'one-level' : scope.row.level == 1 ? 'two-level' : 'three-level'"></el-tag>
                                    {{ scope.row.level == 0 ? '轻微' : scope.row.level == 1 ? '严重' : '非常严重' }}
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="main-page">
                            <el-pagination @size-change="handleSize" @current-change="handleCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                            </el-pagination>
                        </div>
                    </el-card>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
export default {
  data() {
    return {
      abnormalInfoData: [
        {
          name: "好生活屠宰场",
          quarantiner: "张三",
          type: "疾病",
          disease: "口蹄疫",
          level: "0"
        },
        {
          name: "城南屠宰场",
          quarantiner: "李四",
          type: "疾病",
          disease: "口蹄疫",
          level: "1"
        },
        {
          name: "牛牛屠宰场",
          quarantiner: "李思思",
          type: "疾病",
          disease: "口蹄疫",
          level: "2"
        }
      ],
      /** 设备表格每页展示的数据量 */
      pageSize: 15,
      /** 设备表格总数据量 */
      total: 3,
      /** 当前是第几页 */
      page: 1,
      gaugeCharts: null,
      barCharts: null,
      /** 检疫情况柱状图配置 */
      barOptions: {
        title: {
          text: "检疫情况柱状图",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: [
          {
            type: "category",
            name: "日期",
            boundaryGap: true,
            data: [
              "2018-5-1",
              "2018-5-2",
              "2018-5-3",
              "2018-5-4",
              "2018-5-5",
              "2018-5-6",
              "2018-5-7"
            ],
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "次数",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "次数",
            data: [24, 28, 45, 95, 24, 68, 56],
            type: "bar",
            barWidth: "35%",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      /** 合格率仪表图配置项 */
      gaugeOptions: {
        title: {
          text: "肉质检疫合格率",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c}%"
        },
        series: [
          {
            name: "检疫合格率",
            type: "gauge",
            detail: { formatter: "{value}%" },
            data: [{ value: 96.8, name: "检疫合格率" }]
          }
        ]
      },
      slaughteForm: {},
      slaughteRules: {}
    };
  },
  mounted() {
    /** 检疫情况柱状图 */
    let barDom = $(".bar-echarts")[0];
    this.barCharts = this.$echarts.init(barDom);
    this.barCharts.setOption(this.barOptions);

    /** 合格率仪表图 */
    let gaugeDom = $(".gauge-echarts")[0];
    this.gaugeCharts = this.$echarts.init(gaugeDom);
    this.gaugeCharts.setOption(this.gaugeOptions);
  },
  created() {},
  watch: {},
  methods: {
    /** 表格修改每页数量后执行的函数 */
    handleSize(val) {
      this.abnormalInfoData = [];
      this.pageSize = val;
      this.abnormalInfoQuery();
    },

    /** 表格页码改变后执行的函数 */
    handleCurrent(val) {
      this.abnormalInfoData = [];
      this.page = val;
      this.abnormalInfoQuery();
    },

    /** 查询异常信息列表 */
    abnormalInfoQuery() {},

    handleCheckDate() {},

    /** 统计 */
    handleCensus() {},

    /** 重置 */
    handleReset() {}
  }
};
</script>

<style lang="sass">
@import './quarantine_census.scss'
</style>



