<template>
  <div class="login-page">
      <div class="login-form">
          <div class="login-title">
              <img src="../../assets/images/main/favicon-defalut.png">
              <span>屠宰场管理系统</span>
          </div>
          <el-form :model="userForm" :rules="userRules" ref="userForm">
            <el-form-item label="用户名" prop="user">
                <el-input v-model="userForm.user" clearable placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="pwd">
                <el-input type="password" v-model="userForm.pwd" clearable placeholder="请输入密码"></el-input>
            </el-form-item>
          </el-form>
          <div class="bottom-btn">
              <el-button type="primary" round @click="hanleLogin">登录</el-button>
              <el-checkbox v-model="jizhumima">记住密码</el-checkbox>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userForm: {
        user: "",
        pwd: ""
      },
      userRules: {
        user: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 6, message: "长度在 3 到 6 个字符", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 18, message: "长度在 6 到 18 个字符", trigger: "blur" }
        ]
      },
      jizhumima: true
    };
  },

  mounted() {
    let that = this;
    $(".login-form").bind("keydown", function(e) {
      // 兼容FF和IE和Opera
      var theEvent = e || window.event;
      var code = theEvent.keyCode || theEvent.which || theEvent.charCode;
      if (code == 13) {
        that.hanleLogin();
      }
    });
  },

  methods: {
    hanleLogin() {
      this.$refs["userForm"].validate(valid => {
        if (valid) {
          if (this.userForm.user != "cntzc" && this.userForm.user != "admin") {
            this.$popup.showErrorNoti({ message: "用户名不存在" });
          } else {
            let userName = this.userForm.user == "cntzc" ? "城南屠宰场" : "管理员";
            lscache.set("user", this.userForm.user, constants.SESSION_LIMIT); // 用户账号
            lscache.set("userName", userName, constants.SESSION_LIMIT); // 用户姓名
            let path = this.userForm.user == "cntzc" ? "/HomePage" : "/ManageHome";
            this.$router.push(path);
          }
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang="sass">
@import './login.scss';
</style>


