<template>
  <div class="device-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="device-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span>设备管理</span>
                <span class="device-add">
                    <i class="md-icon material-icons" title="添加" @click="handleSaveDevice">&#xE145;</i>
                </span>
            </div>
            <div class="content">
                <div class="demo-input-suffix">
                    <span>设备类型：</span>
                    <el-select v-model="deviceType" clearable placeholder="请选择设备类型">
                        <el-option label="车辆" value="车辆"></el-option>
                    </el-select>
                </div>
                <el-table class="device-table" :data="deviceData" highlight-current-row height="calc(100% - 79px)">
                    <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
                    <el-table-column prop="type" label="类型" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="xinghao" label="型号" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="name" label="名称" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="code" label="编号" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="changjia" label="生产厂家" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="user" label="责任人" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="phone" label="联系电话" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="操作" align="center" width="120">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleUpdateDevice(scope.$index, scope.row)">编辑</el-button>
                            <el-button type="danger" size="mini" @click="handleDeleteDevice(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="main-page">
                    <el-pagination @size-change="handleDeviceSizeChange" @current-change="handleDeviceCurrentChange" :page-sizes="[15, 30, 45, 60]" :page-size="devicePageSize" layout="total, sizes, prev, pager, next, jumper" :total="deviceTotal">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
      deviceType: "",
      deviceData: [
        {
          type: "车辆",
          xinghao: "五菱宏光",
          name: "陕A34323",
          code: "0012054",
          changjia: "五菱",
          user: "张三",
          phone: "13259864587"
        },
        {
          type: "车辆",
          xinghao: "五征",
          name: "陕E34523",
          code: "0152354",
          changjia: "五菱",
          user: "李四",
          phone: "13259864587"
        },
        {
          type: "车辆",
          xinghao: "宝马",
          name: "陕B76234",
          code: "1485234",
          changjia: "五菱",
          user: "王五",
          phone: "13259864587"
        }
      ],
      /** 设备表格每页展示的数据量 */
      devicePageSize: 15,
      /** 设备表格总数据量 */
      deviceTotal: 3,
      /** 当前是第几页 */
      devicePage: 1
    };
  },

  mounted() {},

  created() {},

  watch: {},

  methods: {
    /** 添加 */
    handleSaveDevice() {},
    /** 修改 */
    handleUpdateDevice(index, row) {},
    /** 删除 */
    handleDeleteDevice(index, row) {},

    /** 表格修改每页数量后执行的函数 */
    handleDeviceSizeChange(val) {
      this.deviceData = [];
      this.devicePageSize = val;
      this.deviceDataQuery(1);
    },

    /** 表格页码改变后执行的函数 */
    handleDeviceCurrentChange(val) {
      this.deviceData = [];
      this.devicePage = val;
      this.deviceDataQuery();
    },

    /** 查询设备列表 */
    deviceDataQuery(pg) {
      let param = {
        page: pg || this.devicePage,
        pageSize: this.devicePageSize,
        name: this.clericalName
      };
    }
  }
};
</script>

<style lang="sass">
@import './device_manage.scss'
</style>



